<template>
  <div id="app" style="background: #232f3f;height: 100%">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        is_login: this.$cookies.get("is_login")
      }
    },
    mounted() {
     this.judge();
      //判断登录
      console.log(this.$cookies.get("is_login"));
//      this.download_show();
    },
    methods: {
      judge:function () {
        var url=location.href;
        var is_register=url.indexOf('/register');
        if((this.is_login==0||this.is_login==null)&&is_register==-1)  {
          this.$router.push({path: '/login'});
        }else {

        };
      },
      download_show:function () {
        this.$router.replace({path: '/download'});
      }
    }
  }

</script>



