    post_task(n){
      // console.log(JSON.parse(JSON.stringify(n)));
      let that = this;
      that.$http.post('/acmeCloud/restore/add/task?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          this.$notify({
            message: '新增还原成功',
            type: 'success'
          });
          that.$router.replace({ path: 'restoreDetails?'+'id='+response_info.data});
        } else {
          if(response_info.status==1005){
            this.$notify.error({
              title: '错误',
              message: response_info.msg
            });
          }else{
            this.$notify.error({
              message: '请稍后重试'
            });
          }
        }
      });
    },
