    querySearch_host(queryString, cb){
      var restaurants = this.host_ip_list;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    querySearch_master(queryString, cb) {
      var restaurants = this.matser_list;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      cb(results);
    },
    querySearch(queryString, cb) {
      var restaurants = this.work_list;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(a) {
      return (result) => {
        //这里有一个坑，你如果更改value属性名称代码不报错，结果将无法筛选，永远为空，尝试了多次没有成功，用原生处理筛选，直接赋值cb的参数都不行，一定要属性值为value
        return (result.value.indexOf(a) != -1);
      };
    },
