  mounted() {
    var that = this;
    if (that.$route.query.task_id) {
      that.task_id = that.$route.query.task_id;
    }
    // 数据源高可用配置 实例来源
    var g = 0;
    var heightSource= document.getElementsByName("heightSource");
    // console.log(li)
    for (g = 0; g < heightSource.length; g++) {
      heightSource[g].onclick = function () {
        for (g = 0; g < heightSource.length; g++) {
          heightSource[g].classList.remove("heightSource")
        }
        this.classList.add("heightSource")
      }
    }
    that.getLog(that.task_id)  //获取日志
    that.getTaskdetail(that.task_id)  //获取详情
    that.getGatepara(that.task_id)  //获取同步参数配置列表
    that.curgatesrc(that.task_id)  // 获取数据源列表s
    // console.log(that.ddlList.length)
    for(var i=0;i<that.YX.length;i++) {
      for (var a = 0; a < that.ddlList.length; a++) {
        // console.log(a)
        if (that.YX[i].text == that.ddlList[a].optype) {
          that.a0[a] = i;
        }
      }
    }
    that.a0.push('')
    // let data={//DDL过滤
    //   dbname: "测试",
    //   tablename: "12",
    //   optype: "ALTER TABLE",
    //   action: "ADD INDEX"
    // }
    // this.ddlList.push(data)
    // console.log(this.a0)
    //任务配置-任务高级信息-出错处理
    var a = 0;
    var liHandle = document.getElementsByName("liHandle");
    // console.log(liHandle)
    for (a = 0; a < liHandle.length; a++) {
      liHandle[a].onclick = function () {
        for (a = 0; a < liHandle.length; a++) {
          liHandle[a].classList.remove("liHandle")
        }
        // console.log(111)
        this.classList.add("liHandle")
      }
    }
  },
