    // 同步参数配置修改保存
    savePar:function () {
      if(this.infoList.length>0){
        for (var i=0;i<this.infoList.length;i++){
          if(this.infoList[i].config_value){
            this.infoList[i].config_value=parseInt(this.infoList[i].config_value)
          }
        }
      }
      let data = {
        "task_id":this.task_id,
        "parameter":this.infoList
      }
      // console.log(data)
      this.$http.post('/arkgate/modifygatepara',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          this.$notify({
            message: '修改保存成功',
            type: 'success'
          });
         this.getGatepara(this.task_id)
          this.isUpConfigValue=-1
        } else {
          this.$notify.error({
            title:'保存失败',
            message: '请联系管理员或DBA'
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
