var map = {
  "0":"否",
  "1":"是"
}
export default {
  data(){
    return{
      optionsMySql:[
        'MySQL8.0','MySQL5.7','MySQL5.6','MySQL5.5'
      ],
      templateName:"",
      remark:"",
      tepDfList:[],
      isUpParam:-1,   //是否编辑0.不编辑    1.编辑
      upParamName:"",
      upParamValue:"",
      dbType:"",
      template_type:"",
      version:"MySQL5.7",
      // nameTips:"",
      // remarkTips:""
    }
  },
  filters: {
    toParameName:function (value) {
      var name=value;
      var parameName=name;
      if (name.length>12){
        name=name.substring(0,12)+"..."
      }
      return name;
    },
    toParameText:function (value) {
      return map[value] || ''
    },
    toParameRange:function (value) {
      var range=value;
      var parameRange=range;
      if (range.length>20){
        range=range.substring(0,20)+"..."
      }
      return range;
    },
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        el.focus()
      }
    }
  },
  mounted(){
    this.paramDefaultInfo("MySQL5.7","1");
  },
  methods:{
    //获取参数默认模板
    paramDefaultInfo:function (version,template_type) {
      var param="?template_type="+parseInt(1)+"&version="+version;
      // console.log(template_type);
      // console.log(version)
      this.$http.get('/acmeCloud/parameter/default' + param).then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        // console.log(res)
        if (response_info.status == 0) {
          // 成功
          this.tepDfList=response_info.data;
          // console.log(this.tepDfList);

          // var range = response_info.data.parameter_range;
          // var parameter_range=range;
          // if (range.length>20){
          //   parameter_range=range.substring(0,20)+"..."
          // }
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })
    },
    //修改参数详细
    upParam:function(paramName,paramValue){
      this.isUpParam=paramName;
      this.upParamValue=paramValue;
    },
    //恢复默认值
    rvNameAndValue:function(paramValue){
      // console.log(paramValue)
      // this.upParamName=paramName;
      this.upParamValue=paramValue;
    },
    //保存参数昵称与值
    saveParamNameAndVl:function(paramName,parameter_value){
      this.isUpParam=0;
      let data={
        template_id:this.template_id,
        parameter_name:paramName,
        parameter_value:parameter_value
      }
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/info/single/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          this.templateInfo(this.template_id)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });

    },

    // 新增参数模板
    getAddTemplate:function () {
      if (this.templateName==''||this.remark==''){
        nameTips.innerHTML="模板名称不能为空";
        remarkTips.innerHTML="模板描述不能为空"
        $(window).scrollTop(0)
        return;
      }
      let data={
        template_name:this.templateName,
        remark:this.remark,
        template_type:1,
        version:this.version,
        parameter_info:this.tepDfList
      }
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/add',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          // alert("创建成功!")
          this.$router.push({path: 'parameter'});
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          // alert("创建失败，请稍后再试!")
          alert(response_info.msg)
        }
      }, function (res) {
        // console.log(res)
      });
    },
    blurFn:function () {
      var ipt=document.getElementsByTagName("input");
      //模板名称
      var reg=/^[\da-zA-Z_\u4e00-\u9f5a]{3,8}$/;//为3-8个字符(包含中文,数字,字母和下划线)
      if(ipt[0].value==""){
        ts0.innerHTML="内容不能为空";
        ts0.style.color="red";
      }
      else if(reg.test(ipt[0].value)){
        ts0.innerHTML="";
        ts0.style.color="green";

      }
      else{
        ts0.innerHTML="格式错误";
        ts0.style.color="red";
        ipt[0].value="";//输入错误时输入框就清空
      }
    }
  }
}



