export default {
  data(){
    return{
      restore_data:'',
      restore_log:'',
    }
  },
  mounted() {
    this.get_restore_info();
    this.get_log();
  },
  methods:{
    get_log:function () {
      this.$http.get('/acmeCloud/restore/log?' +
        'id='+this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.restore_log = response_info.data;
        }
      });
    },
    get_restore_info:function () {
      this.$http.get('/acmeCloud/restore/info?' +
        'id='+this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.restore_data = response_info.data;
        } else {
          this.no_list= true
        }
      });
    },
  }
}



