export default {
  data(){
    return{
      hostProblemList:[],    //主机巡检问题列表
      mysqlProblemList:[]        //MySQL巡检问题列表
    }
  },
  mounted(){
    this.zhuji();  //主机
    this.shili();  //MySQL实例数
    this.hamCon(7); //实时巡检
    this.hostProblem(10);   //主机问题
    this.mysqlProblem(10);   //sql巡检问题
  },
  methods: {
//主机
    zhuji() {
      //基于准备好的dom，初始化echarts实例
      var e = 0  // 已拥有台数
      var total=10;   //总台数
      var zhuji = this.$echarts.init(document.getElementById('zhuji'));
      this.$http.get('/acmeCloud/inspection/screen/host/list').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          e=response_info.data;
          total=e*Math.ceil(Math.random()*10)
          // console.log(e)
          //绘制图表
          zhuji.setOption({
            title: {
              show: true,
              text: e + '/台',
              x: 'center',
              y: 'center',
              textStyle: {
                fontSize: '35',
                color: 'white',
                fontWeight: 'normal'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{d}%",
              show: false,
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              show: false
            },
            series:
              {
                name: '',
                type: 'pie',
                radius: ['70%', '75%'],
                avoidLabelOverlap: true,
                hoverAnimation: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#00B7EE'
                      }
                    }
                  },
                  {
                    value: total - e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#3A7181'
                      }
                    }
                  }
                ]
              }
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })

    },
    //MySQL实例数
    shili() {
      //基于准备好的dom，初始化echarts实例
      var e = 0  // 已拥有台数
      var total = 10   //总台数
      var shili = this.$echarts.init(document.getElementById('shili'))
      this.$http.get('/acmeCloud/inspection/screen/mysql/list').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          e=response_info.data;
          total=e*Math.ceil(Math.random()*10);
          // console.log(e);
          //绘制图表
          shili.setOption({
            title: {
              show: true,
              text: e + '/台',
              x: 'center',
              y: 'center',
              textStyle: {
                fontSize: '35',
                color: 'white',
                fontWeight: 'normal'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{d}%",
              show: false,
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              show: false
            },
            series:
              {
                name: '',
                type: 'pie',
                radius: ['70%', '75%'],
                avoidLabelOverlap: true,
                hoverAnimation: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#29F9AD'
                      }
                    }
                  },
                  {
                    value: total - e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#386A58'
                      }
                    }
                  }
                ]
              }
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })
    },
    //实时巡检
    hamCon(cyceltype){   //7.week  30.month  365.year
      var hamCon = this.$echarts.init(document.getElementById('hamCon'));
      // var cycle=['第一天', '第二天', '第三天', '第四天', '第五天', '第六天','第七天']
      // var hostData=[770,670,60,360,983,350,765];
      // var mysqlData=[500,800,600,360,983,350,1899]

      var cycle=new Array()
      var hostData=new Array()
      var mysqlData=new Array()
      var param="?timeLimit="+cyceltype;
      // console.log(param)
      this.$http.get('/acmeCloud/inspection/screen/echart' + param).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          var timeLimitList=response_info.data;
          // console.log(timeLimitList)
          for (var i=0;i<timeLimitList.length;i++){
            cycle.push(timeLimitList[i].date);
            hostData.push(timeLimitList[i].host_data);
            mysqlData.push(timeLimitList[i].mysql_data)
          }
          var maxMysql=Math.max(...mysqlData)
          var maxHost=Math.max(...hostData)
          var maxY=maxMysql;
          if(maxY<maxHost){
            maxY=maxHost
          }
          //
          // console.log(Math.max(...mysqlData))
          // console.log(Math.max(...hostData))
          // console.log(cycle)
          // console.log(hostData)
          // console.log(mysqlData)
          hamCon.setOption({
            backgroundColor: 'white',
            title: {
              text: '',
              textStyle: {
                fontWeight: 'normal',
                fontSize: 12,
                color: 'black'
              },
              left: '6%'
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                lineStyle: {
                  color: '#57617B'
                }
              }
            },
            legend: {
              icon: 'rect',
              itemWidth: 14,
              itemHeight: 5,
              itemGap: 13,
              data: ['主机巡检','MySQL巡检'],
              right: '4%',
              textStyle: {
                fontSize: 16,
                color: '#000'
              }
            },
            grid: {
              left: '8%',
              right: '2%',
              bottom: '8%',
              top:'14%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              boundaryGap: false,
              axisLine: {
                lineStyle: {
                  color: '#57617B'
                }
              },
              data: cycle,  //X 轴时间
              axisLabel:{
                interval:0,
                rotate:50,
                textStyle:{
                  fontSize:12
                }
              },
            }],
            yAxis: [{
              type: 'value',
              gridIndex:0,
              min:0,
              max:Math.ceil(maxY+maxY*0.1),
              axisTick: {
                show: false
              },
              axisLine: {
                show:true,
                lineStyle: {
                  color: '#57617B'
                }
              },
              axisLabel: {
                textStyle: {
                  fontSize: 12
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#57617B'
                }
              }
            }],
            series: [{
              name: '主机巡检',
              type: 'line',
              smooth: true,
              lineStyle: {
                normal: {
                  width: 3
                }
              },
              areaStyle: {
                // normal: {
                //   color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                //     offset: 0,
                //     color: 'rgba(82, 191, 255, 0.3)'
                //   }, {
                //     offset: 0.8,
                //     color: 'rgba(82, 191, 255, 0)'
                //   }], false),
                //   shadowColor: 'rgba(228, 139, 76, 0.1)',
                //   shadowBlur: 10
                // }
              },
              symbolSize:10,
              itemStyle: {
                normal: {
                  color: 'rgb(82, 191, 255)',
                  borderColor:'#193444'
                },
              },
              data: hostData,   //主机巡检
            },
              {
                name: 'MySQL巡检',
                type: 'line',
                smooth: true,
                lineStyle: {
                  normal: {
                    color:'#77F4B2',
                    width: 3
                  }
                },
                areaStyle: {
                  // normal: {
                  //   color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  //     offset: 0,
                  //     color: 'rgba(137, 189, 27, 0.3)'
                  //   }, {
                  //     offset: 0.8,
                  //     color: 'rgba(137, 189, 27, 0)'
                  //   }], false),
                  //   shadowColor: 'rgba(0, 0, 0, 0.1)',
                  //   shadowBlur: 10
                  // }
                },
                symbolSize:10,
                itemStyle: {
                  normal: {
                    color: '#77F4B2',
                    borderColor:'#193444'
                  },
                },
                data: mysqlData   // mysql巡检
              } ]
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })
    },
    //主机巡检问题
    hostProblem:function (displayNum) {
      var pram = "?displayNum="+displayNum;
      // console.log(pram)
      this.$http.get('/acmeCloud/inspection/screen/host/problem'+pram).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.hostProblemList=response_info.data
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      })
    },
    //mysql巡检问题
    mysqlProblem:function (displayNum) {
      var pram = "?displayNum="+displayNum;
      // console.log(pram)
      this.$http.get('/acmeCloud/inspection/screen/mysql/problem'+pram).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.mysqlProblemList=response_info.data
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      })
    }
  }
}



