export default {
  data() {
    return {
      account_host_tips:'',
      account_host:'',
      es_account:'',
      esaccount_tips:'',
      espassword_tips:'',
      esport_tips:'',
      esaccount:'',
      espassword:'',
      password_tips:'',
      account_tips:'',
      ip_tips:'',
      port_tips:"",
      rootname_tips:'',
      rootpaseeword_tips:'',
      button_type:1,
      button:'',
      times:5,
      percent:0,
      mask_show:false,
      userAaaount:'',
      agent:'',
      escheck:'5',
      esport:'',
      es:'3',
      radio: '1',
      name:'',
      account:'',
      password:'',
      ip:'',
      port:'',
      rootName:'',
      rootpaseeword:'',
    }
  },
  mounted: function(){
    // console.log('download挂载到dom后');
    // this.draw();
    this.check_resource();
    // this.getData();
  },
  methods:{
    check_es(){//es检查
    if(this.account_host==''){
      this.account_host_tips='主机不能为空！'
    }
      if(this.esport==''){
        this.esport_tips='端口不能为空！'
      }
      if(this.escheck ==6){
        if(this.espassword==''){
          this.espassword_tips='密码不能为空！'
        }
        if(this.esaccount==''){
          this.esaccount_tips='账号不能为空！'
        }
        if(this.account_host&&this.esport&&this.espassword&&this.esaccount){
          var n ={
            es_ip:this.account_host,
            es_port:parseInt(this.esport),
            es_user:this.esaccount,
            es_pass:this.espassword
          }
          this.$http.post('/acmeCloud/checkES?',
            JSON.stringify(n), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'},
              emulateJSON: true
            }).then(function (res) {
            var response_info = res.body;
            if (response_info.status == 0) {
              alert('connect ES test succuss！')
            } else {
              alert(response_info.msg);
            }
          });
        }
      }else{
        if(this.account_host&&this.esport){
          var n ={
            es_ip:this.account_host,
            es_port:parseInt(this.esport),
            db_user:'',
            db_pass:''
          }
          this.$http.post('/acmeCloud/checkES?',
            JSON.stringify(n), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'},
              emulateJSON: true
            }).then(function (res) {
            var response_info = res.body;
            if (response_info.status == 0) {
              alert('connect ES test succuss！')
            } else {
              alert(response_info.msg)
            }
          });
        }
      }
    },
    check_mysql(){//mysql检查
      if(this.port ==''){
        this.port_tips='端口不能为空！'
      }
      if(this.ip==''){
        this.ip_tips='IP不能为空！'
      }
      if(this.account==''){
        this.account_tips = '账号不能为空！'
      }
      if(this.password==''){
        this.password_tips = '密码不能为空！'
      }
      if(this.port&&this.ip&&this.account&&this.password){
        var n ={
          db_ip:this.ip,
          db_port:parseInt(this.port),
          db_user:this.account,
          db_pass:this.password
        }
        this.$http.post('/acmeCloud/checkMySQL?',
          JSON.stringify(n), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            emulateJSON: true
          }).then(function (res) {
          var response_info = res.body;
          if (response_info.status == 0) {
           alert('test succuss！')
          } else {
            alert(response_info.msg);
          }
        });
      }
    },
    down_load(){//点击下载按钮
      this.button=1;
      // this.$http.get('/acmeCloud/download'
      // ).then(function (res) {
      //   var response_info = res.body;
      //   if (response_info.status==0) {
      //   } else {
      //     alert(response_info.msg);
      //   }
      // });
    },
    check_resource(){//检查是否在下载
          this.$http.get('/acmeCloud/checkResource'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info.data);
          if(response_info.data != 0){
            this.button_type =0;
          }
        } else {

        }
      });
    },
    submit_form(){
      var that = this;
      this.mask_show = true;
      var timer = setInterval(function () {
        if(that.percent<= 100){
          that.draw(that.percent++);
        }else{
          clearInterval(timer);
          var five = setInterval(function () {
            that.times--;
            if(that.times == 0){
              clearInterval(five);
              that.times =0;
              that.$router.replace({ path: '/login' })
            }
          },1000)

        }
      },100)
    },
    draw(e){
      var data = [e];
      var mask = this.$echarts.init(document.getElementById('mask'));
      mask.setOption( {
        title: {
          text: '完成度',
          show: false
        },
        xAxis: {
          show: false
        },
        yAxis: [{
          show: false,
          data: []
        }, {
          show: false,
          data: []
        }],
        series: [{
          name: '条',
          type: 'bar',
          yAxisIndex: 0,
          data: data,
          barWidth: 30,
          itemStyle: {
            normal: {
              color: '#18a1c1',

            }},
          label: {
            normal: {
              show: true,
              position: 'inside',
              formatter: '{c}%'
            }},
        }, {
          name: '框',
          type: 'bar',
          yAxisIndex: 1,
          barGap: '-100%',
          data: [100],
          barWidth: 40,
          itemStyle: {
            normal: {
              color: 'none',
              borderColor: '#18a1c1',
              borderWidth: 5,
              barBorderRadius: 5,
            }
          }
        },]
      })
    }
  }
}



