var map = {
  "0": "运行中",
  "1": "失败",
  "2": "成功",
  "3": "暂停",
  "4": "清理",
  "5": "重试",
  "6": "清理完成"
}
var status = {   //status
  "0": "运行中",
  "1": "失败",
  "2": "成功",
  "3": "暂停",
  "4": "清理",
  "5": "重试",
  "6": "清理完成"
}
export default {
  data() {
    return {
      task_id: "",
      task_name: "", //任务名称
      architecture_type: "",  //任务类型
      task_status: "", //任务状态
      deploy_port: "",  //端口
      create_time: "", //创建时间
      namespace: "", //NAMESPACE
      version: "",  //版本
      task_details: [],
      step_info: [],
      clearips:"", //清理ip
      retryips:"", //重试ip
      warmMsg: "",  //警告信息
      allWarmMsg: "",   //全部重试的警告信息
      errormsg:"",   //错误信息
      infolist:0
    }
  },
  filters: {
    toStatusText: function (value) {
      return map[value] || ''
    },
    toStepstatusText: function (value) {
      return status[value] || ''
    },
    toMsg:function (value) {
      var msg =value.replace(';',';\n')
      // console.log(msg)
      return msg
    },
  },
  mounted() {
    this.getInterval();
    this.getMysqlInfo(this.task_id);
  },
  updated: function () {
    var i = 0;
    var li = document.getElementsByName("li");
    for (i = 0; i < li.length; i++) {
      li[i].onclick = function () {
        for (var i = 0; i < li.length; i++) {
          li[i].classList.remove("li");
        }
        this.classList.add("li")
      }
    }
    // console.log(this.step_info)
  },
  methods: {
    getInterval(){
      var that = this;
      if (this.$route.query.task_id) {
        this.task_id = this.$route.query.task_id;
        var timer = setInterval(function () {
          that.getMysqlInfo(that.task_id);
          // console.log(that.task_status);
          if (that.task_status == 1 || that.task_status == 2 || that.task_status == 6) {
            clearInterval(timer);
          }
        }, 1000)
      }
    },
    getMysqlInfo: function (task_id) {    //部署任务详情
      var param = "?task_id=" + task_id;
      this.$http.get('/acmeCloud/deploy/mysql/detail' + param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.task_id = response_info.data.task_id;
          this.task_name = response_info.data.task_name;
          this.architecture_type = response_info.data.architecture_type;
          this.task_status = response_info.data.task_status;
          this.deploy_port = response_info.data.deploy_port;
          this.create_time = response_info.data.create_time;
          this.namespace = response_info.data.namespace;
          this.version = response_info.data.version;
          this.task_details = response_info.data.task_details;
          if (this.task_details.length > 0) {
            this.step_info = this.task_details[this.infolist].step_info
          }
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    deployhalt: function (instance_ip) {   //暂停 单个
      let data = {
        "task_id": this.task_id,
        "instance_ip": instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/deployhalt',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("暂停成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    },
    restart: function (instance_ip) {  // 暂停后重新开始 单个
      let data = {
        "task_id": this.task_id,
        "instance_ip": instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/restart',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("重新开始成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    },
    skip: function (instance_ip) { //失败跳过 单个
      // console.log("点了跳过")
      let data = {
        task_id: this.task_id,
        instance_ip: instance_ip,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/skip',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("跳过成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("跳过失败")
          this.$notify({
            type: 'error',
            message: '跳过失败，请联系管理员或DBA'
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
    retry: function () {  //单个重试
      // console.log("点了单个重试")
      let data = {
        task_id: this.task_id,
        instance_ip: this.retryips,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/retry',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("单个重试成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          this.errorMsg=response_info.msg
          this.$notify({
            type: 'success',
            message: this.errorMsg
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
    clean: function () {  //单个清理
      // console.log("点了单个清理")
      let data = {
        task_id: this.task_id,
        instance_ip: this.clearips,
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/clean',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("单个清理成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    },
    warm: function () {  //警告信息接口
      var param = "?task_id=" + this.task_id;
      this.$http.get('/acmeCloud/deploy/mysql/warn' + param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          // console.log("获取警告信息成功")
          this.warmMsg = response_info.data
          // console.log(response_info.data)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    allDeployhalt: function () {  //全部暂停
      let data = {
        "task_id": this.task_id,
        "instance_ip": "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/deployhalt',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("全部暂停成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    },
    allRetry: function () { //全部重试
      // console.log("点了全部重试")
      let data = {
        task_id: this.task_id,
        instance_ip: "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/retry',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("全部重试成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    },
    allClean: function () {  //全部清理
      // console.log("点了全部清理")
      let data = {
        task_id: this.task_id,
        instance_ip: "",
      }
      // console.log(data)
      this.$http.post('/acmeCloud/deploy/mysql/clean',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          // console.log("全部清理成功")
          this.getMysqlInfo(this.task_id);
          this.getInterval();
          // location.reload(true);//添加成功后刷新页面
        } else {
          // 说明调用接口失败，可能由于路径，网络问题

        }
      }, function (res) {
        // console.log(res)
      });
    }
  }
}



