export default {
  data() {
    return {
      account_id: "",
      is_login: this.$cookies.get("is_login")
    }
  },
  mounted() {
    // console.log(this.$cookies.get("is_login"))
    // console.log(this.$cookies.get("account_name"))
    // console.log(this.get("is_login"))
    // this.$cookies.remove("is_login")
    // this.delete("is_login")
  },
  methods: {
    tabEn: function () {
      this.$i18n.locale = "en";
    },
    tabCn: function () {
      this.$i18n.locale = 'cn'
    },
    //退出
    loginOut: function () {
      this.$http.get('/acmeCloud/signout', {withCredentials: true}).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // alert("退出成功");
          window.location.href = "/"
          sessionStorage.clear()
          // session.removeItem("collapse_index");
          // session.removeItem("collapse_body");
        } else {
          // alert("退出失败")
        }
      });
    },

  }
}



