export default {
  data() {
    return {
      data_all: '',
      failedList:"",
      progress:"",
      successnum:"",
      totalnum:""
    }
  },
  mounted() {
    this.get_detail();
    this.failedListAll(); //录入失败
    this.getInterval();
  },
  methods: {
    getInterval(){
      var that = this;
      var timer = setInterval(function () {
        that.get_detail(); //录入成功
        that.entryRate();//获取进度条
        that.entrySuccessnum(); //获取成功个数
        that.entryTotalnum(); //获取总个数
        that.failedListAll(); //录入失败
        if (that.data_all.entry_status == 1 || that.data_all.entry_status == 2) {
          // console.log(that.data_all.entry_status)
          clearInterval(timer)
        }
      }, 2000)
    },
    get_detail() { //录入成功
      this.$http.get('/acmeCloud/host/entry/successandrunning?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          this.data_all = response_info.data;
          if (this.host_list) {
            this.host_list.replace(/,/g, '\n');
          }
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },
    failedListAll:function () { //录入失败
      this.$http.get('/acmeCloud/host/entry/failed?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          this.failedList = response_info.data;
          // console.log("执行了~~")
          if (this.host_list) {
            this.host_list.replace(/,/g, '\n');
          }
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },
    entryRate:function () { //获取进度条
      this.$http.get('/acmeCloud/host/entry/rate?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          this.progress = response_info.data.sucess_rate;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },
    entryRetry:function () { //失败重试
      this.$http.get('/acmeCloud/host/entry/retry?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          this.failedList = response_info.data;
          this.data_all=""//初始1化
          this.get_detail();
          this.failedListAll(); //录入失败
          this.getInterval();
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },
    entrySuccessnum:function () {  //获取成功个数
      this.$http.get('/acmeCloud/host/entry/successnum?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          this.successnum = response_info.data;
          // console.log( response_info.data)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },
    entryTotalnum:function () {  //获取总个数
      this.$http.get('/acmeCloud/host/entry/totalnum?' +
        "entryId=" + this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          this.totalnum = response_info.data;
          // console.log( response_info.data)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    }
  }
}



