export default {
  data(){
    return{
      account_name:"",
      account_password:""
    }
  },
  mounted: function(){
    // console.log('login挂载到dom后');
  },
  destroyed:function(){
    location.reload()         //点击登录后返回上一级刷新页面
  },
  methods:{
    login:function () {
      var result = this.params_check();
      let data={
        account_name: this.account_name,
        account_password: this.account_password
      }
      this.$http.post( '/acmeCloud/login',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          withCredentials: true,
          emulateJSON: true
        }).then(function (res) {
        // console.log(res)
        var response_info = res.body;
        if (response_info.status == 0) {
          // console.log(this.$cookies.get("is_login"));
          window.location.href = "/info/hostList"
        } else {
          returnMsg.innerHTML=response_info.msg;
          returnMsg.style.color = "#F15533";
        }
      }, function (res) {

      });
    },
    register:function () {
      this.$router.push({path: '/register'});
    },
    params_check: function () {
      if (this.account_name == "") {
        alert("username is empty");
        return false
      }
      if (this.account_password == "") {
        alert("user password is empty");
        return false
      }
      return true
    }
  }
}



