export default {
  data(){
    return{
      total: 0,
      current_page: 1,
      current: 1,
      currentPage:1,
      page_size:10,
      list:[
        {a:'a'},{a:'b'},{a:'c'}
      ],
      restore_list:[],
      total_num:'',
      no_list:false,

    }
  },
  filters: {
    toParameName:function (value) {
      var name=value;
      var parameName=name;
      if (name.length>12){
        name=name.substring(0,12)+"..."
      }
      return name;
    },
  },
  mounted() {
    this.setCurrent(1);
  },
  props: {
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods:{
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      this.$http.get('/acmeCloud/restore/mysql/template/total').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          this.total = response_info.data;

        }
      });
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      this.$http.get('/acmeCloud/restore/mysql/template/page?'+
        "current_page="+this.current_page+"&page_size="+this.display).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.restore_list = response_info.data;
        } else {
        }
      });
    },
    //
    get_instance_list(){
      this.$http.get('/acmeCloud/restore/list?'+
        "currentPage="+this.currentPage+"&pageSize="+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.restore_list =  response_info.data;
        }
      });
    },
    get_total(){
      this.$http.get('/acmeCloud/restore/total?').then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.total_num = response_info.data;
        } else {
        }
      });
    },
    go(e){//翻页
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      var num = e;//页数
      this.currentPage = e;
      this.$http.get('/acmeCloud/restore/list?' +
        'current_page='+e+'&page_size='+this.page_size).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.restore_list = response_info.data;
          if(this.restore_list.length < 10){
            this.no_list= true
          }else{
            this.no_list= false
          }
        } else {
          this.no_list= true
        }
      });
    },
    detele_id(id){//删除操作
      this.$http.get('/acmeCloud/restore/delete/mysql/template?id='+id).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.$notify({
            message: '删除成功',
            type: 'success',
            duration:3000,
          });
          this.setCurrent(1);
        }else{
          if (response_info.status==2004) {
            this.$notify({
              message: '不存在该模板',
              type: 'warning'
            });
          }else{
            this.$notify.error({
              message: '删除出错，请稍后重试'
            });
          }
        }

      });
    },
  }
}



