export default {
  data(){
    return{
      cluster_checksum:[],
    }
  },
  mounted(){
    this.get_check_num();
  },
  filters:{
    toValue:function (value) {
      var queryvalue=Math.ceil(value);
      return queryvalue;
    }
  },
  methods:{
    get_check_num(){//
      if(this.$route.query.clusterName){
        this.$http.get('/acmeCloud/slowQuery/checksum/cluster?'+
          'clusterName='+this.$route.query.clusterName+'&checkSum='+this.$route.query.checksum).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.cluster_checksum = response_info.data;
          } else {
          }
        });
      }else{
        this.$http.get('/acmeCloud/slowQuery/checksum/instance?'+
          'instanceName='+this.$route.query.instanceName+'&checkSum='+this.$route.query.checksum).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.instance_checksum = response_info.data;
          } else {
          }
        });
      }

    },
  }
}



