import Vue from 'vue';
export  default {
  data(){
    return{
      show:true,
      last:0,
      model_path:'路径配置',
      config_list:[],
      info_list:[],
      config_value:false,
      model_index:'',
      backup_item:['backup-item'],
      show_model:{
        display:'none'
      },
      model_path_new:'',
      model_path_placehold:'',
    }
  },
  directives: {

    focus: {

      inserted: function (el, {value}) {

        // console.log(el,{value})

        if (value) {

          el.focus();

        }

      }

    }

  },
  mounted(){
    this.get_backup_config_info();
  },
  methods:{
    delete_config(e){

    },
    hide_backup(){
      this.show = false
    },
    hide_model(){
      this.show_model = {
        display:'none',
      }
    },
    config_list_delete(i,e){
      // this.config_list[e].config_list.splice(i,1);
      var n = {
        config_name:this.config_list[e].config_name,
        config_value:this.config_list[e].config_list[i]
      };
      this.$http.post('/acmeCloud/backup/config/delete',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // console.log('删除');

          this.config_list[e].config_list.splice(i,1);
        } else {
          // console.log(response_info);
        }
      });
    },
    config_list_append(){
      this.config_list[this.model_index].config_list.push(this.model_path_new);
      var n = {
        config_name:this.config_list[this.model_index].config_name,
        config_name_alias:this.config_list[this.model_index].config_name_alias,
        config_value:$.trim(this.model_path_new),
      };
      this.$http.post('/acmeCloud/backup/config/add',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(n);
        if (response_info.status == 0) {
          // console.log('修改成功');
          this.show_model = {
            display:'none',
          }
          this.model_path_new = '';
        } else {

        }
      });
    },
    backup_plus(e){
      this.show_model = {
        display:'block',
      }
      this.model_index = e;
      this.model_path = this.config_list[e].config_name_alias;
      this.model_path_placehold = this.config_list[e].config_list[0];
    },
    plus_config(e){
      this.config_list[e].show = true;
      Vue.set(this.config_list);
    },
    config_cancel(e){
      // console.log(e);
      this.config_list[e].show = false;
      Vue.set(this.config_list);
    },
    delete_li(i,l){//删除某项备份
      // console.log(this.config_list[l].config_list);
      this.config_list[l].config_list.splice(i,1);
      var n ={
        config_name:this.config_list[l].config_name,
        config_value:this.config_list[l].config_list[i],
      };
      this.$http.post('/acmeCloud/backup/config/delete?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(n);
        if (response_info.status == 0) {
          // console.log('修改成功');
        } else {

        }
      });

    },
    get_backup_config_info(){
      this.$http.get('/acmeCloud/backup/config/info'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.config_list = response_info.data;
          this.config_list.map(function(item){
            item.show =false
          });
        } else {
        }
      });
    },
  }
}



