export default {
  data(){
    return {
      confirm_instance_name:'',//待删除集群名称
      centerDialogVisible:false,
      total: 0,
      current_page: 1,
      current: 1,
      no_acitve:'disabled',
      no_item:'',
      currentPage:1,
      pageSize:10,
      show_pagination:true,
      show_alert:false,
      cluster_page_num:'',
      cluster_num:[],
      change_result:'',
      colony_list:[]
    }
  },
  mounted(){
    this.setCurrent(1);
    // this.get_data();//获取页面数据
    // this.get_page_num();//判断页数
  },
  props: {
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods: {
    // 分页
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      //总条数
      this.$http.get('/acmeCloud/MySQLCluster/total').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {this.total = response_info.data; } else {
        }
      }, function (res) {
        // console.log(res)
      });
      // 分页  部署列表
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      this.$http.get('/acmeCloud/MySQLCluster/list?'+
        "currentPage="+this.current_page+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // 成功
          this.colony_list = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    },

    get_data(){
      this.$http.get('/acmeCloud/MySQLCluster/list?'+
        "currentPage="+this.currentPage+"&pageSize="+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // 成功
          this.colony_list = response_info.data

        } else {
        }
      });
    },
    //获取页数
    get_page_num(){
      this.$http.get('/acmeCloud/MySQLCluster/total').then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // 成功
          this.cluster_num = response_info.data;
          if(response_info.data/10>1){
            this.show_pagination = true;
            this.cluster_page_num = Math.ceil(this.cluster_num/10);
          }else{
            this.show_pagination = false;
          }
        } else {
        }
      });
    },
    go(e){//翻页
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      var that = this;
      var num = e;//页数
      this.currentPage = e;
      this.$http.get('/acmeCloud/MySQLCluster/list?'+
        "currentPage="+e +"&pageSize="+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          if(response_info.data.length < 10){
            this.colony_list = response_info.data;
            this.no_item = true;
          }else{
            this.colony_list = response_info.data;
            this.no_item = false;
          }

        } else {//没有数据了
          this.no_item = true;
        }
      });
    },
    delete_instance(e){//实例删除
      this.confirm_instance_name = e;
    },
    sure_delete(){
      var that = this;
      // console.log('确认删除');
      var n = {
        cluster_name: that.confirm_instance_name
      }
      that.$http.post('/acmeCloud/MySQLCluster/deletecluster?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // console.log('删除成功');
          that.centerDialogVisible = false;
          that.setCurrent(1);
        }
      });
    }
  },
}



