export default {
  mounted() {
    var router_link=document.getElementsByName("font");
    var a=0
    for (a=0;a<router_link.length;a++){
      router_link[a].onclick=function () {
        for (a=0;a<router_link.length;a++){
          router_link[a].classList.remove("words")
        }
        this.classList.add("words");
      }
    }

    var i=0;
    var span=document.getElementsByTagName("span");
    for (i=0;i<span.length;i++){
      span[i].onclick=function () {
        for (i=0;i<span.length;i++){
          span[i].classList.remove("words")
        }
        this.classList.add("words");
      }
    }
    var b=0;
    var libg=document.getElementsByName("libg");
    for (b=0;b<libg.length;b++){
      libg[b].onclick=function () {
        for (b=0;b<libg.length;b++){
          libg[b].classList.remove("libg")
        }
        this.classList.add("libg");
      }
    }
  this.check_collapse();
  },
  methods:{
    check_collapse(){
      if(sessionStorage.getItem("collapse_body_index")){
        var words_all=document.getElementsByTagName("span");
        for(var a = 0;a<words_all.length;a++){//去掉所有的一级菜单高亮
          words_all[a].classList.remove("words");
        }
        var span_i=document.getElementsByName("span_words");
        span_i[sessionStorage.getItem("collapse_body_index")].classList.add("words");
        var collapse_body = document.getElementsByName("collapse_body");
        collapse_body[sessionStorage.getItem("collapse_body_index")].classList.add("in");//折叠框打开
        var libg=document.getElementsByName("libg");
        var router_link=document.getElementsByName("font");
        for (var b=0;b<libg.length;b++){
          libg[b].classList.remove("libg");
        }
        libg[sessionStorage.getItem("collapse_index")].classList.add("libg");
        router_link[sessionStorage.getItem("collapse_index")].classList.add("words");

      }
    },
    active_banner(e,f){  //二级菜单
      var exp = undefined;
      sessionStorage.setItem("collapse_index", e);//存储一级菜单
      if(f == exp){
      }else{
        sessionStorage.setItem("collapse_body_index", f);//存储二级菜单
      }
    }
  }
}



