export default {
  data(){
    return{
      data: this.generateData(),
      value1: [],
      contact_way:'',
      contact_type:[
        {
          label:'dingding',
          key:'钉钉',
          enable:false,
        },{
          label:'text',
          key:'短信',
          enable:false,
        },{
          label:'wecheat',
          key:'微信',
          enable:false,
        },{
          label:'email',
          key:'邮件',
          enable:false,
        }
      ],
      checkAll: false,
      isIndeterminate: true,
      alert_confs:[
        {
          name:"磁盘报警",
          content:"解释说明",
          type:0,
          dimension:"disk",
          metric:"used_percent",
          fatal:90,
          warn:80,
          judge_rule:">",
          readonly:false,
          enable:true    //是否开启
        },
        {
          name:"pxc报警",
          content:"解释说明",
          type:0,
          dimension:"pxc",
          metric:"wsrep_local_state",
          fatal:2,
          judge_rule:"=",
          readonly:true,
          enable:false
        }
      ],
    }
  },
  mounted(){

  },
  methods:{
    handleCheckAllChange(val) {
      this.alert_confs.forEach(function (v,i){
        v.enable = val;
      })
    },
    generateData(){
      const data = [];
      const people_list = [{
        "account_id":"",
        "account_name":"aaa",
        "tel":"",
        "email":"",
        "role_id":"",
        "role_name":"",
        "role_name_alias":"",
        "wechat":"",
        "dingding":""
      },
        {
          "account_id":"",
          "account_name":"bbb",
          "tel":"",
          "email":"",
          "role_id":"",
          "role_name":"",
          "role_name_alias":"",
          "wechat":"",
          "dingding":""
        }]

      people_list.forEach(function (v,j,a) {
        data.push({
          key: j,
          label:v.account_name,
        });
        // for (let i = 1; i <= a.length; i++) {
        //   data.push({
        //     key: i,
        //     label:v.account_name,
        //   });
        // }
      })
      // console.log(data);
      return data;
    }
  }
}



