import Vue from 'vue'
export default {
  data(){
    return{
      detailes:'',
    }
  },
  created(){

  },
  mounted(){
    this.mysql_template_id()
  },
  methods:{
    mysql_template_id() {
      this.$http.get('/acmeCloud/restore/mysql/template/byid?id='+this.$route.query.id).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.detailes = response_info.data;

        }
      });
    },
    detele_id(id){
      this.$http.get('/acmeCloud/restore/delete/mysql/template?&id='+id).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.$notify({
            duration:3000,
            message: '删除成功',
            type: 'success',
            onClose:()=>{
              this.$router.replace('restoreTemplate')
            }
          });
        }else{
          if (response_info.status==2004) {
            this.$notify({
              message: '不存在该模板',
              type: 'warning'
            });
          }else{
            this.$notify.error({
              message: '删除出错，请稍后重试'
            });
          }
        }

      });
    },
  }

  ,

}



