export default {
  data(){
    return{
      currentPage:1,
      pageSize:10,
      toptimesum:[],
      clusterinfo_list:[],
      cluster_base_info:[],
      top_time_sum:[],
      top_count_data:[],
      top_avg_time_data:[],
      time_info:'暂无数据',
      info:'本实例未采集到数据',
      time_total:'暂无数据',
      top_avg_info:'暂无数据'
    }
  },
  filters:{
    toValue:function (value) {
      var queryvalue=Math.ceil(value);
      return queryvalue;
    }
  },
  mounted(){
    this.get_base_info();
    this.get_list();
  },
  methods:{
    get_base_info(){
      this.$http.get('/acmeCloud/slowQuery/cluster/info?'+
        'clusterName='+this.$route.query.clusterName).then(function (res) {
        var response_info = res.body;

        if (response_info.status==0) {
          // console.log(response_info.data);
          this.cluster_base_info = response_info.data;
        } else {
        // console.log(response_info);
        this.time_info = response_info.msg
        }
      });
    },
    top_count(){
      this.$http.get('/acmeCloud/slowQuery/cluster/topcount?'+
        'clusterName='+this.$route.query.clusterName+'&displayNum='+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.top_count_data = response_info.data;

        } else {
          // console.log(response_info);
          this.time_total = response_info.msg

        }
      });
    },
    top_avg_time(){
      this.$http.get('/acmeCloud/slowQuery/cluster/topavgtime?'+
        'clusterName='+this.$route.query.clusterName+'&displayNum='+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.top_avg_time_data = response_info.data;
          // console.log(response_info);
        } else {
          // console.log(response_info);
          this.top_avg_info = response_info.msg;
        }
      });
    },
    get_list(){
      var that= this;
      this.$http.get('/acmeCloud/slowQuery/cluster/toptimesum?'+
        'clusterName='+this.$route.query.clusterName+'&displayNum='+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.top_time_sum = response_info.data;
          //console.log(response_info.data);
        } else {
          that.time_info = response_info.msg;
        }
      });
    }
  }
}



