export default {
 data(){
   return{
     chang_disage:'',
     isUpdate:999,
     hots_list:[
       {
         name:'disk',
         early_warning:1,
         warning:10,
         rule:'磁盘报警'
       },
       {
         name:'cpu',
         early_warning:2,
         warning:20,
         rule:'cpu报警'
       },
       {
         name:'cpu',
         early_warning:2,
         warning:20,
         rule:'cpu报警'
       }
       ,
       {
         name:'cpu',
         early_warning:3,
         warning:30,
         rule:'cpu3报警'
       }
     ]
   }
 },
  mounted(){
      this.init();
  },
  methods:{
    init(){
      this.hots_list.map(function (item) {
        item.show = true;
      })
    },
    modify(e){
      this.isUpdate = e;
    },
    modify_sure(e){
      this.isUpdate = 9999;
    }
  }
}



