export default {
  data(){
    return{
      options:[{
        value: '5.6',
        label: '5.6'
      },{
        value: '5.7',
        label: '5.7'
      }],
      name:'',
      mark:'',
      mysql_version:'',
      install_mysql_cmd:'',
      start_mysql_cmd:'',
      stop_mysql_cmd:'',
      mycnf_file:'',
      mysql_base_dir:'',
      mysql_data_dir:'',
    }
  },
  mounted() {
     if(this.$route.query.id){
       let id = this.$route.query.id;
       this.$http.get('/acmeCloud/restore/mysql/template/byid?id='+id).then(function (res) {
         var response_info = res.body;
         if (response_info.status==0) {
           this.name = response_info.data.name;
           this.mark = response_info.data.mark;
           this.install_mysql_cmd = response_info.data.install_mysql_cmd;
           this.start_mysql_cmd= response_info.data.start_mysql_cmd;
           this.stop_mysql_cmd= response_info.data.stop_mysql_cmd;
           this.mycnf_file = response_info.data.mycnf_file;
           console.log(response_info.data.mycnf_file);
           this.mysql_base_dir = response_info.data.mysql_base_dir;
           this.mysql_data_dir = response_info.data.mysql_data_dir;
           this.mysql_version =  response_info.data.mysql_version;
         }
       });
     }
  },
  methods:{
    updata_mysql_tem(){//更新
      this.check_input();
      let data = [];
      if(this.name&&this.mark&&this.mysql_version&&this.install_mysql_cmd&&this.start_mysql_cmd&&this.stop_mysql_cmd&&this.mycnf_file&&this.mysql_base_dir&&this.mysql_data_dir){
        data={
          id:parseInt(this.$route.query.id),
          name:this.name,
          mark:this.mark,
          mysql_version:this.mysql_version,
          install_mysql_cmd: this.install_mysql_cmd,
          start_mysql_cmd: this.start_mysql_cmd,
          stop_mysql_cmd: this.stop_mysql_cmd ,
          mycnf_file: this.mycnf_file,
          mysql_base_dir: this.mysql_base_dir,
          mysql_data_dir: this.mysql_data_dir
        }
        console.log(data);
        this.$http.post('/acmeCloud/restore/update/mysql/template',
          JSON.stringify(data), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            emulateJSON: true
          }).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.$notify({
              title: '成功',
              message: '添加成功',
              type: 'success'
            });
            this.$router.replace('restoreTemplate');
          } else {
            if (response_info.status==2001) {
              this.$notify.error({
                message: '该模板已存在'
              });
            }else{
              this.$notify.error({
                message: '请稍后重试'
              });
            }
          }
        });
      }

    },
      check_input(){
          if(this.name){
            if(this.mark){
              if(this.mysql_version){
                if(this.install_mysql_cmd){
                  if(this.start_mysql_cmd){
                    if(this.stop_mysql_cmd){
                      if(this.mycnf_file){
                        if(this.mysql_base_dir){
                          if(this.mysql_data_dir){
                          }else{
                            this.$notify({
                              message: 'datadir不能为空',
                              type: 'warning'
                            });
                          }
                        }else{
                          this.$notify({
                            message: 'basedir不能为空',
                            type: 'warning'
                          });
                        }
                      }else{
                        this.$notify({
                          message: 'mysql配置路径及文件不能为空',
                          type: 'warning'
                        });
                      }
                    }else{
                      this.$notify({
                        message: '关闭mysql命令不能为空',
                        type: 'warning'
                      });
                    }
                  }else{
                    this.$notify({
                      message: '启动mysql命令不能为空',
                      type: 'warning'
                    });
                  }
                }else{
                  this.$notify({
                    message: '安装mysql命令不能为空',
                    type: 'warning'
                  });
                }
              }else{
                this.$notify({
                  message: 'MySQL版本不能为空',
                  type: 'warning'
                });
              }
            }else{
              this.$notify({
                message: '模板描述不能为空',
                type: 'warning'
              });
            }
          }else{
            this.$notify({
              message: '模板名称不能为空',
              type: 'warning'
            });
          }

      },
      add_mysql_tem(){//模板添加
        this.check_input();
        let data = [];
        if(this.name&&this.mark&&this.mysql_version&&this.install_mysql_cmd&&this.start_mysql_cmd&&this.stop_mysql_cmd&&this.mycnf_file&&this.mysql_base_dir&&this.mysql_data_dir){
          data={
           name:this.name,
           mark:this.mark,
           mysql_version:this.mysql_version,
           install_mysql_cmd:this.install_mysql_cmd,
           start_mysql_cmd:this.start_mysql_cmd,
           stop_mysql_cmd:this.stop_mysql_cmd ,
            mycnf_file:this.mycnf_file,
           mysql_base_dir:this.mysql_base_dir,
           mysql_data_dir:this.mysql_data_dir
       }
       console.log(data);
        this.$http.post('/acmeCloud/restore/add/mysql/template',
          JSON.stringify(data), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            emulateJSON: true
          }).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.$notify({
              title: '成功',
              message: '添加成功',
              type: 'success'
            });
            this.$router.replace('restoreTemplate');
          } else {
            this.$notify.error({
              message: '请稍后重试'
            });
          }
        });
        }
    },
  },
}



