import Vue from 'vue';
export default {
  data(){
    return{
      backup_machine_mfs_dir_config_item:'',
      backup_machine_mfs_config_item:'',
      trans_limit_config_item:'',
      backup_bin_path_config_item:'',
      local_machine_path_config_item:'',
      local_backup_path_config_item:'',
      backup_path_config_item:'',
      mysql_path_config_item:'',
      handleRow_tips:'',
      handleRow_index:'',
      backup_machine_mfs_dir:'',
      backup_machine_mfs:'',
      trans_limit:'',
      backup_bin_path:'',
      local_machine_path:'',
      local_backup_path:'',
      backup_path:'',
      mysql_path:'',
      no_instcante_info:'',
      backup_err:false,
      grade:'',
      type:'',
      radio: '1',
      radio2:3,
      radio3:6,//没有备份池
      select_instance_name:'',
      instance_list:[],
      value: '',
      config_list:[],
      backup_list:[],
      host_index:0,
      check_instance_name:[
        {
          instance_name:''
        }
      ],
      instance_name:'',
      mysql_dir:0,
      msg:'0',//是否有备份池
      tar:'1',//是否压缩
    }
  },
  mounted(){
    this.get_backup_config_info();
    this.get_base_info();//获取实例名称
    this.time();
  },
  methods:{
    handleRow(item) {
      this.handleRow_index =item;
    },
    change_radio(item){
      this.radio3 = item;
    },
    time(){
      this.start_time = this.get_day(1);
      },
    get_base_info(){
      this.clustername = this.$route.query.clusterName;
      this.$http.get('/acmeCloud/MySQLCluster/baseinfo?'
        +'clusterName='+this.$route.query.clusterName
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          if(response_info.data.length !=0){
            this.instance_info = response_info.data;
            this.check_instance_name = this.instance_info;
            var arr = [];
            for(var i = 0;i<response_info.data.length;i++){
              var obj = {};
              obj.value =i;
              obj.label = response_info.data[i].instance_name;
              arr.push(obj);
            }
            this.instance_list = arr;
            this.value = response_info.data[0].instance_name;
            this.handleRow_index = 0;
          }else{
            this.instance_list = [
              {value:'该集群没有实例数据，无法备份',
                label:'该集群没有实例数据，无法备份',
              }
            ];
            this.no_instcante_info = '该集群没有实例数据，无法备份';
          }
        } else {
        }
      });
      this.$http.get('/acmeCloud/MySQLCluster/clusterinfo?'
        +'clusterName='+this.$route.query.clusterName
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.grade = response_info.data.cluster_grade;
          this.type = response_info.data.cluster_type;
        } else {
        }
      });
    },
    get_backup_config_info(){
      var that = this;
      this.$http.get('/acmeCloud/backup/config/info'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          response_info.data.map(function (item) {
            item.tips = '';
          })
          response_info.data.forEach(function (v,i,a){
            if(v.config_name =='MySQL_CNF_PATH'){
              that.mysql_path = v;
              that.mysql_path_config_item = v.config_list[0];

            }
            if(v.config_name=='BACKUP_MACHINE'){
              that.backup_path = v;
              that.backup_path_config_item = v.config_list[0];
            };
            if(v.config_name=='LOCAL_BACKUP_DIR'){
              that.local_backup_path = v;
              that.local_backup_path_config_item = v.config_list[0];
            };
            if(v.config_name=='BACKUP_MACHINE_DIR'){
              that.local_machine_path = v;
              that.local_machine_path_config_item = v.config_list[0];
            };
            if(v.config_name=='BACKUP_BIN_DIR'){
              that.backup_bin_path = v;
              that.backup_bin_path_config_item = v.config_list[0];
            };
            if(v.config_name=='TRANS_LIMIT'){
              that.trans_limit = v;
              that.trans_limit_config_item = v.config_list[0];
            };
            if(v.config_name=='BACKUP_MACHINE_MFS'){
              that.backup_machine_mfs = v;
              that.backup_machine_mfs_config_item = v.config_list[0];
            };
            if(v.config_name=='BACKUP_MACHINE_MFS_DIR'){
              that.backup_machine_mfs_dir = v;
              that.backup_machine_mfs_dir_config_item = v.config_list[0];
            };
          })
        } else {
        }
      });
    },
    change_info(e,i){
      // console.log(i);
      this.host_index = i;
      this.instance_name = e.instance_name;
    },
    get_day(day){
      var today = new Date();
      var h = today.getHours(); //获取当前小时数(0-23)
      var m = today.getMinutes(); //获取当前分钟数(0-59)
      var s = today.getSeconds(); //获取当前秒数(0-59)
      var targetday_milliseconds = today.getTime() + 1000*60*60*24*day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      tMonth =this. doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return tYear+"-"+tMonth+"-"+tDate+' 00-00-00';
    },
    doHandleMonth(month){
      var m = month;
      if(month.toString().length == 1){
        m = "0" + month;
      }
      return m;
    },
    seleect_mysql_path_chage(item){
       // console.log('seleect_mysql_path_chage');
      this.mysql_path.config_item = item;
    },
    mysql_path_chage(item){
      // console.log(item);

      // this.mysql_path.splice(1,1,item);
      // console.log( this.mysql_path.splice())
      // this.mysql_path.config_item = item;
      // this.seleect_mysql_path_chage(item);
    },

    post_deploy(){
      var that = this;
      if(this.handleRow_index!=''){
      }else{
        this.handleRow_tips = '实例名不能为空';
      }
      if(this.mysql_path_config_item){
      }else{
        this.mysql_path.tips = 'MySQL配置文件路径不能为空';
      }
      if(this.backup_path_config_item){
      }else{
        this.backup_path.tips = '备份机机器地址不能为空';
      }
      if(this.local_backup_path_config_item){
      }else{
        this.local_backup_path.tips = '本地备份存储临时目录不能为空';
      }
      if(this.local_machine_path_config_item){

      }else{
        this.local_machine_path.tips = '备份机备份存储目录不能为空';
      }
      if(this.backup_bin_path_config_item){

      }else{
        this.backup_bin_path.tips = '备份工具路径不能为空'
      }
      if(this.trans_limit_config_item){

      }else{
        this.trans_limit.tips = '传输速度不能为空'
      }
      if(this.backup_machine_mfs_config_item){

      }else{
        this.backup_machine_mfs.tips = '备份池机器地址不能为空'
      }
      if(this.backup_machine_mfs_dir_config_item){

      }else{
        this.backup_machine_mfs_dir.tips = '备份池备份存储目录不能为空'
      }
      if(that.radio3 == 3){//存在备份池
        if(that.handleRow_index >=0){
          if(that.mysql_path_config_item&&that.backup_path_config_item&&that.local_backup_path_config_item
            &&that.local_machine_path_config_item&&that.local_machine_path_config_item&&that.backup_bin_path_config_item&&
            that.trans_limit_config_item){
            var n = {
              cluster_name:that.$route.query.clusterName,//集群名
              ip:that.check_instance_name[that.handleRow_index].host_ip,//实例地址
              port:parseInt(that.check_instance_name[that.handleRow_index].instance_port),//实例端口
              hostname:that.check_instance_name[that.handleRow_index].host_name,//主机名
              backup_type:"hot_full",//备份类型
              cluster_grade:parseInt(that.grade),//集群等级
              cluster_type:parseInt(that.type),//集群类型
              backup_set_keep:parseInt(0),// 备份集保留时间
              conf_dir:that.mysql_path_config_item,//MySQL配置文件路径
              local_backup_info_dir:that.local_backup_path_config_item,//本地存储目录
              backup_machine:that.backup_path_config_item,//备份机器
              backup_dir:that.local_machine_path_config_item,//备份机存储目录
              tar_dir:'',
              mfs_flag:parseInt(1),//是否有备份池
              mfs_machine:'',//备份池机器
              mfs_dir:'',//备份池存储目录
              // is_tar:parseInt(this.tar),//是否压缩
              next_backup_date:that.get_day(1),
              xtrabackup_bin_path:that.backup_bin_path_config_item,//备份工具目录
              trans_limit:that.trans_limit_config_item,//传输速度
              mfs_machine:that.backup_machine_mfs_config_item,//备份池机器
              mfs_dir:that.backup_machine_mfs_dir_config_item//备份池存储目录
            }
            that.$http.post('/acmeCloud/backup/deploy?',
              JSON.stringify(n), {
                headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                emulateJSON: true
              }).then(function (res) {
              var response_info = res.body;
              if (response_info.status == 0) {
                this.$router.replace({ path: 'backupDetail?'+'name='+this.$route.query.clusterName});
              } else {
                this.backup_err = true;
              }
            });
          }
        }else{
          this.handleRow_tips = '实例名不能为空';
        }

      }
      if(this.radio3 ==6 ){//没有备份池
        if(that.handleRow_index >=0){
          if(that.mysql_path_config_item&&that.mysql_path_config_item&&that.backup_path_config_item&&that.local_backup_path_config_item&&that.local_machine_path_config_item&&that.backup_bin_path_config_item&&that.trans_limit_config_item){
            var n = {
              cluster_name:this.$route.query.clusterName,//集群名
              ip:that.check_instance_name[that.handleRow_index].host_ip,//实例地址
              port:parseInt(that.check_instance_name[that.handleRow_index].instance_port),//实例端口
              hostname:that.check_instance_name[that.handleRow_index].host_name,//主机名
              backup_type:"hot_full",//备份类型
              cluster_grade:parseInt(that.grade),//集群等级
              cluster_type:parseInt(that.type),//集群类型
              backup_set_keep:parseInt(0),// 备份集保留时间
              conf_dir:that.mysql_path_config_item,//MySQL配置文件路径
              local_backup_info_dir:that.local_backup_path_config_item,//本地存储目录
              backup_machine:that.backup_path_config_item,//备份机器
              backup_dir:that.local_machine_path_config_item,//备份机存储目录
              tar_dir:'',
              mfs_flag:parseInt(0),//是否有备份池
              mfs_machine:'',//备份池机器
              mfs_dir:'',//备份池存储目录
              // is_tar:parseInt(this.tar),//是否压缩
              next_backup_date:that.get_day(1),
              xtrabackup_bin_path:that.backup_bin_path_config_item,//备份工具目录
              trans_limit:that.trans_limit_config_item,//传输速度
            }
            that.$http.post('/acmeCloud/backup/deploy?',
              JSON.stringify(n), {
                headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                emulateJSON: true
              }).then(function (res) {
              var response_info = res.body;
              // console.log(n);
              if (response_info.status == 0) {
                // console.log('部署成功');
                that.$router.replace({ path: 'backupDetail?'+'name='+this.$route.query.clusterName});
              } else {
                that.backup_err = true;
              }
            });
          }
        }else{
          this.handleRow_tips = '实例名不能为空';
        }
      }

    },
  },
}



