export default {
  data(){
    return{
      slowquery_button_index:99,
      total: 0,
      current_page: 1,
      current: 1,
      currentPage:1,
      pageSize:10,
      slowquery_list:[],
      dis_abled:[],
      show_pagination:true,
      no_active:false,
      dis_abled:''
    }
  },

  props: {
    // total: {// 数据总条数
    //   type: Number,
    //   default: 10
    // },
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    // currentPage: {// 当前页码
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  mounted(){
    this.setCurrent(1);
  },

  methods:{
    // 分页
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      //总条数
      this.$http.get('/acmeCloud/slowQuery/total').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          this.total = response_info.data;} else {
        }
      }, function (res) {
      });
      // 分页  部署列表
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      this.$http.get('/acmeCloud/slowQuery/list?'+
        "currentPage="+this.current_page+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.slowquery_list = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
        }
      });
    },
    go(e){//翻页
      document.documentElement.scrollTop = document.body.scrollTop = 0;
      var num = e;//页数
      this.currentPage = e;
      this.$http.get('/acmeCloud/slowQuery/list?'+
        "currentPage="+e +"&pageSize="+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.slowquery_list = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
          // console.log(response_info.data);
          if(response_info.data.length < 10){
            this.dis_abled = 'disabled';
            this.no_active = true;
          }else{
            this.no_active = false;
            this.dis_abled = [];
          }
        } else {
          // console.log(response_info);
          this.dis_abled = 'disabled';
          this.no_active = true;
          if(response_info.status ==1013){
            // console.log('没有数据');
            this.currentPage = this.currentPage -1;
          }

        }
      });
    },
      get_list(){
        this.$http.get('/acmeCloud/slowQuery/list?'+
          '&currentPage='+this.currentPage+'&pageSize='+this.pageSize).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.slowquery_list = response_info.data;
          } else {
          }
        });
      }
  }
}



