export default {
  data(){
    return{
      isRouterAlive: true,
      cluster_name_model:this.$route.query.name,//修改的集群名称
      cluster_name_modify:false,
      grade_show:true,
      select_options:[
        {value:1,
        label:'无需备份'},
        {value:2,
          label:'不重要'},
        {value:3,
          label:'普通'},
        {value:4,
          label:'重要'},
        {value:5,
          label:'非常重要'},

      ],
      change_result : '修改成功',
      show_alert:false,
      change_remark_value:true,
      cluster_baseinfo:[],
      cluster_base_info:[],
      slowQueryStatistics:[],
      no_slow_query_echert:null,
      selected_value:1,
      remark_info:'',
      selected_value_text:'无需备份',
      slow_Statistics:{slow_query_list:{}
  },
      info_num:10,
      zero:'00:00:01',
      backup_value:true,
      show_cluster: 'in active',
      show_topo:''
    }
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        // console.log('ddd');
        el.focus()
      }
    }
  },
  created(){
    this.get_data();
    this.get_statue();
  },
  mounted(){
    // this.draw();
    // this.get_data();
   // this.get_slowQuery_Statistics();
  },
  methods:{
    get_statue(){
      // console.log(this.$route.query.topology)
      if(this.$route.query.topology){
        // console.log('ssssssssss');
        this.show_cluster='';
          this.show_topo='in active';
      }
    },
    change_remark(){
      this.change_remark_value = !this.change_remark_value;
    },
    remark_modify(){//备注信息修改
      var n ={
        cluster_name:this.$route.query.name,
        remark:$.trim(this.remark_info),
      };
      this.$http.post('/acmeCloud/MySQLCluster/remark/modify?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(n);
        if (response_info.status == 0) {
          // console.log('修改成功');
          this.cluster_base_info.remark = $.trim(this.remark_info);
          this.change_remark_value = !this.change_remark_value;

          that.show_alert=true;
          setTimeout(function () {
            that.show_alert=false;
          },1000)
        } else {
          // console.log(response_info);

        }
      });
    },
    change_backup(){//备份等级选择
      var that= this;
      this.backup_value= !this.backup_value;
    },
    get_day(day){//获取日期
      var today = new Date();
      var targetday_milliseconds = today.getTime() + 1000*60*60*24*day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      tMonth =this. doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return tYear+"-"+tMonth+"-"+tDate;
    },
    doHandleMonth(month){
      var m = month;
      if(month.toString().length == 1){
        m = "0" + month;
      }
      return m;
    },
    copyArr(arr) {
      let res = [];
      for (let i = 0; i < arr.length; i++) {
        res.push(arr[i])
      }
      return res
    },
    list(){
         return Array.prototype.slice.call(arguments);
    },
    reload () {
      this.isRouterAlive = false;
      this.$nextTick(() => (this.isRouterAlive = true))
    },
    grade_modify(){
      if(this.selected_value ==1){
        this.selected_value_text = '无需备份';
        this.cluster_base_info.backup_cycle = 0;
      }else if(this.selected_value ==2){
        this.selected_value_text = '不重要';
        this.cluster_base_info.backup_cycle = 7;
      }else if(this.selected_value ==3){
        this.selected_value_text = '普通';
        this.cluster_base_info.backup_cycle = 5;
      }
      else if(this.selected_value ==4){
        this.selected_value_text = '重要';
        this.cluster_base_info.backup_cycle = 3;
      }
      else if(this.selected_value ==5){
        this.selected_value_text = '非常重要';
        this.cluster_base_info.backup_cycle =1;
      }
      this.backup_value = true;
      var that = this;
      var n ={
        cluster_name:this.$route.query.name,
        cluster_grade:parseInt(this.selected_value),
      };

      this.$http.post('/acmeCloud/MySQLCluster/grade/modify?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(n);
        if (response_info.status == 0) {
            // console.log('修改成功');
          that.show_alert=true;
          this.grade_show=false;
          setTimeout(function () {
            that.show_alert=false;

          },1000);

        } else {

        }
      });
    },
    get_slowQuery_Statistics(){
      var that= this;
      this.$http.get('/acmeCloud/slowQuery/cluster/statistics?'+
        "clusterName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          that.slow_Statistics = response_info.data;
          var arr_num = new Array();
          var arr_time = new Array();
          if(that.slow_Statistics){//慢查询有图
            that.slow_Statistics.forEach(function (v,i,a) {
              var i =  v.instance_name;
              var d = v.slow_query_list;
              d.forEach(function (q,i,a) {
               arr_time.push(q.time);
                arr_num.push(q.slow_query_num);
                var slow_query = that.$echarts.init(document.getElementById('slow_query_cluster_statistics'));
                slow_query.setOption({
                  title:{
                    text:'总数据量:',
                    textStyle:{
                      color:'#fff',
                      fontSize:16,
                    },
                    left:20,
                    top:10
                  },
                  tooltip:{
                    show:true
                  },
                  grid:{
                    x:50,
                    y:50
                  },
                  xAxis: {
                    boundaryGap: false,
                    show:true,
                    type: 'category',
                    nameLocation:'start',
                    boundaryGap: false,
                    axisLine:{
                      lineStyle:{color:'#000'}
                    },
                    data: arr_time,
                    nameTextStyle:{
                      color:'#fff',
                      fontSize:18
                    },
                  },
                  yAxis:[
                    {
                      type: 'value',
                      gridIndex: 0,
                      splitLine: {
                        show: false
                      },
                      axisTick: {
                        show: false
                      },
                      min: 0,
                      max: Math.max(...arr_num),
                      axisLine: {
                        lineStyle: {
                          color: '#0c3b71'
                        }
                      },
                      axisLabel: {
                        color: 'rgb(170,170,170)',
                        formatter: '{value}'
                      }
                    },
                    {
                      type: 'value',
                      gridIndex: 0,
                      min: 0,
                      max: Math.max(...arr_num),
                      splitNumber: 12,
                      splitLine: {
                        show: false
                      },
                      axisLine: {
                        show: false
                      },
                      axisTick: {
                        show: false
                      },
                      axisLabel: {
                        show: false
                      },
                      splitArea: {
                        show: true,
                        areaStyle: {
                          color: ['rgba(250,250,250,0.0)', 'rgba(250,250,250,0.05)']
                        }
                      }
                    }
                  ],
                  series: [{
                    name:'总数据量XQ',
                    data:arr_num,
                    type: 'line',
                    smooth: true,
                    label:{
                      show:true,
                      color:'#0049B0',
                    },
                    areaStyle: {
                      type:'line'
                    },
                    symbolSize:10,
                    itemStyle:{
                      normal:{
                        label : {show: true},
                        color:'#00B7EE',
                        borderColor:'#fff',
                      }
                    },
                  }]
                })
              })
            })
          }else{//慢查询没有图
            that.no_slow_query_echert = '暂无数据'
          }
        } else {
        }
      });
    },
    cluster_name_modify_sure(){
      var that = this;
      var n ={
        old_cluster_name:that.$route.query.name,
        new_cluster_name:that.cluster_name_model
      };
      that.$http.post('/acmeCloud/MySQLCluster/clusternamemodify?',
        JSON.stringify(n), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          that.cluster_name_modify = false;
          that.cluster_base_info.cluster_name=that.cluster_name_model;
          // console.log(that.cluster_name_model);
          that.$router.go(-1);
        } else {

        }
      });
    },
    get_data(){
      this.$http.get('/acmeCloud/MySQLCluster/baseinfo?'+
        "clusterName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // 成功
          // console.log(response_info.data);
          this.cluster_baseinfo = response_info.data;
          this.info_num = response_info.data.length;

        } else {
        }
      });
      this.$http.get('/acmeCloud/MySQLCluster/clusterinfo?'+
        "clusterName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;

        if (response_info.status==0) {
          this.cluster_base_info = response_info.data;
          // console.log(response_info);
        } else {
        }
      });
    },
    draw(){
        var warning_1 = this.$echarts.init(document.getElementById('warning_1'));
        warning_1.setOption({
          title:{
            text:'告警总数:',
            textStyle:{
              color:'#000',
              fontSize:14,
            }
          },
          color:['#D15ECD','#D15ECD'],
          series:[
            {
              type:'pie',
              name:'pie1',
              radius:['55%','70%'],
              center:['50%','60%'],
              data:[
                {
                  value:100,name:'数量',
                  label: {
                    normal: {
                      position: 'center',
                      formatter: '{c|{c}} {text|台}',
                      color: '#fff',
                      rich: {
                        c: {
                          fontWeight: 'bold',
                          color: '#fff',
                          fontSize: 40
                        },
                        text: {
                          color: '#fff'
                        }
                      }
                    }
                  },
                  itemStyle:{
                    normal: {
                      color: '#D15ECD',
                      shadowColor: '#D15ECD',
                      shadowBlur: 10
                    }
                  }
                },
              ]
            },
            {
              type:'pie',
              name:'pie2',
              radius:['65%','70%'],
              center:['50%','60%'],
              data:[
                {
                  value:100,name:'数量',
                  label: {
                    normal: {
                      position: 'center',
                      formatter: '{c|{c}} {text|台}',
                      color: '#fff',
                      rich: {
                        c: {
                          fontWeight: 'bold',
                          color: '#fff',
                          fontSize: 40
                        },
                        text: {
                          color: '#fff'
                        }
                      }
                    }
                  },
                  itemStyle:{
                    normal: {
                      color: '#fff',
                      shadowColor: '#fff',
                      shadowBlur: 10
                    }
                  }
                },
              ]
            },

          ]
        })
      }
  }
}



