export default {
  data() {
    return {}
  },
  mounted() {
    this.draw();//服务器数量
    this.colony(); //集群
    this.nodes();  //节点
    this.backups(); //备份
    this.deploy(); //部署
  },
  methods: {
    jumpList:function () {   //点击服务器数量跳到主机列表
      this.$router.push({path: 'hostList'});
    },
    jumpMannagement:function () {    //点击集群数量跳到集群列表
      this.$router.push({path: 'management'});
    },
    jumpInstance:function () {   //点击节点数量跳到实例列表
      this.$router.push({path: 'instancelist'});
    },
//服务器数量
    draw() {
      //基于准备好的dom，初始化echarts实例
      var e = 0;  // 已拥有台数
      var total = 100;   //总台数
      var conOne = this.$echarts.init(document.getElementById('conOne'));
      this.$http.get('/acmeCloud/system/host/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          e = response_info.data;
          total=e*Math.ceil(Math.random()*10)
          // console.log(e);
          // console.log(total)
          //绘制图表
          conOne.setOption({
            title: {
              show: true,
              text: e,
              x: 'center',
              y: 'center',
              textStyle: {
                fontSize: '35',
                color: '#000',
                fontWeight: 'normal'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{d}%",
              show: false,
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              show: false
            },
            series:
              {
                name: '',
                type: 'pie',
                radius: ['70%', '75%'],
                avoidLabelOverlap: true,
                hoverAnimation: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#32F02D'
                      }
                    }
                  },
                  {
                    value: total - e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#88A686'
                      }
                    }
                  }
                ]
              }
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })
    },
    // 集群数量
    colony() {
      var e = 0;  // 已拥有台数
      var total = 100;   //总台数
      var conTwo = this.$echarts.init(document.getElementById('conTwo'));
      this.$http.get('/acmeCloud/system/cluster/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          e = response_info.data;
          total=e*Math.floor(Math.random()*10+1);
          // console.log(e);
          // console.log(total);
          //绘制图
          conTwo.setOption({
            title: {
              show: true,
              text: e,
              x: 'center',
              y: 'center',
              textStyle: {
                fontSize: '35',
                color: '#000',
                fontWeight: 'normal'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{d}%",
              show: false,
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              show: false
            },
            series:
              {
                name: '',
                type: 'pie',
                radius: ['70%', '75%'],
                avoidLabelOverlap: true,
                hoverAnimation: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#F5DD1A'
                      }
                    }
                  },
                  {
                    value: total - e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#C1BB85'
                      }
                    }
                  }
                ]
              }
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //节点数量
    nodes() {
      var e = 0; // 已拥有台数
      var total = 100;   //总台数
      var conThree = this.$echarts.init(document.getElementById('conThree'));
      this.$http.get('/acmeCloud/system/instance/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          e = response_info.data;
          total=e*Math.floor(Math.random()*10+1);
          // console.log(e);
          // console.log(total);
          //绘制图
          conThree.setOption({
            title: {
              show: true,
              text: e,
              x: 'center',
              y: 'center',
              textStyle: {
                fontSize: '35',
                color: '#000',
                fontWeight: 'normal'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{d}%",
              show: false,
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              show: false
            },
            series:
              {
                name: '',
                type: 'pie',
                radius: ['70%', '75%'],
                avoidLabelOverlap: true,
                hoverAnimation: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#E5004F'
                      }
                    }

                  },
                  {
                    value: total - e, name: '',
                    itemStyle: {
                      normal: {
                        color: '#BF758E'
                      }
                    }
                  }
                ]
              }
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    // 备份
    backups() {
      var yx = 0; //已执行
      var yxTotal = 100;   //总额
      var jx = 0;  //进行中
      var jxTotal = 100;   //总额
      var beifeng = this.$echarts.init(document.getElementById('beifeng'));
      var yc = 0;    //现在异常数量
      var ycTotal=100;   //异常总数
      this.$http.get('/acmeCloud/system/backup/status').then(function (res) {
        var response_info = res.body;
        // console.log(response_info)
        if (response_info.status == 0) {
          // 成功
          yx = response_info.data.backup_task_complete_total;    //已完成
          yxTotal = response_info.data.backup_task_complete_percentage;    ///需完成总额
          jx = response_info.data.backup_task_running_total;   //进行中
          jxTotal = response_info.data.backup_task_running_percentage; //需进行总数
          yc = response_info.data.backup_task_error_total;    //现在异常数量
          ycTotal = response_info.data.backup_task_error_percentage  //异常总数
          // console.log(yx)
          beifeng.setOption({
            title: {
              text: '备份',
              textStyle: {
                color: '#000'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
              x: 'center',
              y: 'bottom',
              data: ['已完成', '异常', '进行中'],
              textStyle: {
                fontSize:16,
                color: '#000'
              }
            },
            series: [
              {
                name: '概况',
                type: 'pie',
                hoverAnimation: false,
                clockwise: false,
                startAngle: 160,
                radius: ['25%', '30%'],
                labelLine: {
                  normal: {
                    show: true,
                    length: 20,
                    length2: 20
                  },
                },
                label: {
                  normal: {
                    formatter: '{b}: {c} ({d}%) ',
                    "textStyle":{
                      fontSize:15
                    }
                  }
                },
                data: [
                  {
                    value: yx, name: '已完成',
                    itemStyle: {
                      normal: {
                        color: '#22AC38'
                      }
                    }
                  },
                  {
                    value: yxTotal, name: '总额',
                    itemStyle: {
                      normal: {
                        color: '#AAAAAA'
                      }
                    },
                    label: {
                      normal: {
                        show: false
                      }
                    },
                    labelLine: {
                      normal: {
                        show: false,
                        lineStyle: {
                          color: "#395166"
                        }

                      }
                    }
                  }
                ]
              },
              {
                name: '概况',
                type: 'pie',
                clockwise: false,
                startAngle: 95,
                hoverAnimation: false,
                radius: ['5%', '10%'],
                label: {
                  normal: {
                    formatter: '{b}: {c} ({d}%) ',
                    "textStyle":{
                      fontSize:15
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: true,
                    length: 30,
                    length2: 30
                  },

                },
                data: [
                  {
                    value: yc, name: '异常',
                    itemStyle: {
                      normal: {
                        color: '#F39800'
                      }
                    }
                  },
                  {
                    value: ycTotal, name: '总额',
                    itemStyle: {
                      normal: {
                        color: '#AAAAAA'
                      }
                    },
                    label: {
                      normal: {
                        show: false
                      }
                    },
                    labelLine: {
                      normal: {
                        show: false,
                        lineStyle: {
                          color: "#395166"
                        }

                      }
                    }
                  }
                ]
              },
              {
                name: '概况',
                type: 'pie',
                hoverAnimation: false,
                radius: ['40%', '45%'],
                startAngle: 135,
                labelLine: {
                  normal: {
                    show: true,
                    length: 20,
                    length2: 10
                  }
                },
                label: {
                  normal: {
                    formatter: '{b}: {c} ({d}%) ',
                    "textStyle":{
                      fontSize:15
                    }
                  }
                },
                data: [
                  {
                    value: jx, name: '进行中',
                    itemStyle: {
                      normal: {
                        color: '#01B7EE'
                      }
                    }
                  },
                  {
                    value: jxTotal, name: '总额',
                    itemStyle: {
                      normal: {
                        color: '#AAAAAA'
                      }
                    },
                    label: {
                      normal: {
                        show: false
                      }
                    },
                    labelLine: {
                      normal: {
                        show: false,
                        lineStyle: {
                          color: "#395166"
                        }
                      }
                    }
                  }
                ]
              }
            ]
          })
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    // 部署
    deploy() {
      var bushu = this.$echarts.init(document.getElementById('bushu'));
      bushu.setOption({
        title: {
          text: '部署',
          textStyle: {
            color: '#000'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          x: 'center',
          y: 'bottom',
          data: ['已完成', '异常', '进行中'],
          textStyle: {
            fontSize:16,
            color: '#000'
          }
        },
        series: [
          {
            name: '概况',
            type: 'pie',
            hoverAnimation: false,
            clockwise: false,
            startAngle: 45,
            radius: ['25%', '30%'],
            labelLine: {
              normal: {
                show: true,
                length: 20,
                length2: 20
              },
            },
            label: {
              normal: {
                formatter: '{b}: {c} ({d}%) '
              }
            },
            data: [
              {
                value: 5, name: '已完成',
                itemStyle: {
                  normal: {
                    color: '#22AC38'
                  }
                }
              },
              {
                value: 10, name: '总额',
                itemStyle: {
                  normal: {
                    color: '#AAAAAA'
                  }
                },
                label: {
                  normal: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false,
                    lineStyle: {
                      color: "#395166"
                    }

                  }
                }
              }
            ]
          },
          {
            name: '概况',
            type: 'pie',
            clockwise: false,
            startAngle: 95,
            hoverAnimation: false,
            radius: ['5%', '10%'],
            label: {
              normal: {
                formatter: '{b}: {c} ({d}%) '
              }
            },
            labelLine: {
              normal: {
                show: true,
                length: 30,
                length2: 35
              },

            },
            data: [
              {
                value: 5, name: '异常',
                itemStyle: {
                  normal: {
                    color: '#F39800'
                  }
                }
              },
              {
                value: 10, name: '总额',
                itemStyle: {
                  normal: {
                    color: '#AAAAAA'
                  }
                },
                label: {
                  normal: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false,
                    lineStyle: {
                      color: "#395166"
                    }

                  }
                }
              }
            ]
          },
          {
            name: '概况',
            type: 'pie',
            hoverAnimation: false,
            radius: ['40%', '45%'],
            labelLine: {
              normal: {
                show: true,
                length: 20,
                length2: 10
              }
            },
            label: {
              normal: {
                formatter: '{b}: {c} ({d}%) '
              }
            },
            data: [
              {
                value: 5, name: '进行中',
                itemStyle: {
                  normal: {
                    color: '#01B7EE'
                  }
                }
              },
              {
                value: 20, name: '总额',
                itemStyle: {
                  normal: {
                    color: '#AAAAAA'
                  }
                },
                label: {
                  normal: {
                    show: false
                  }
                },
                labelLine: {
                  normal: {
                    show: false,
                    lineStyle: {
                      color: "#395166"
                    }
                  }
                }
              }
            ]
          }
        ]
      })
    }
  }
}



