export default {
  data() {
    return {
      account_id:this.$cookies.get("account_id"),
      total: 0,       //分页
      currentPage: 1,   //分页
      current: this.currentPage,  //分页
      inspectionList:[],
      is_info:0,
    }
  },
  filters: {
    toValue:function (value) {
      var insvalue=value;
      var sqlvalue=insvalue;
      if (insvalue.length>26){
         insvalue=insvalue.substring(0,26)+"..."
       }
       return insvalue;
    },
    toTablevalue:function (value) {
      var tableValue=Math.ceil(value/1024/1024)
      tableValue+="M"
      return tableValue
    },
    tobinlogValue:function (value) {
      var binlog=Math.ceil(value/1024/1024)
      binlog+="M"
      return binlog
    },
    toInfo:function (value) {   //表碎片详细数据转换M
      var infos=Math.ceil(value/1024/1024)
      infos+="M"
      return infos;
    },
    toPercentage:function (value) {
      var name=Number(value*100).toFixed(2);
      // name+="%"
      return name;
    },
    toSqlPercentage:function (value) {
      var name=Math.floor(Number(value*100));
      return name;
    }
    //   var sqlvalue=Number(value/1024/1024);
    //   sqlvalue+="M"
    //   return sqlvalue;
    // }
  },

  mounted(){
    this.setCurrent(1,4);
    var i=0;
    // var td=document.getElementsByClassName("td");
    var td=document.getElementsByName("td");
    for (i=0;i<td.length;i++){
      td[i].onclick=function () {
        for (i=0;i<td.length;i++){
          td[i].classList.remove("td")
        }
        this.classList.add("td");
      }
    }
  },
  props: {
    // total: {// 数据总条数
    //   type: Number,
    //   default: 10
    // },
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    // currentPage: {// 当前页码
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods: {
    // 分页
    //idx 为页面 configType为巡检项类型  默认传0  为内存使用率
    setCurrent: function (idx,configType) {
      // console.log(idx);
      this.inspectionList = [];
      this.total=0;
      // console.log(configType);
      this.is_info=configType
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.currentPage = this.current;
        this.$emit('pagechange', this.current);
      }
      // 分页  主机列表
      var param="currentPage="+this.currentPage+"&pageSize="+this.display+"&configType="+configType;
      // console.log(param)
      this.$http.get('/acmeCloud/inspection/list?'+
        param).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.inspectionList = response_info.data.inspectionList;
          this.total=response_info.data.total;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    }
  }
}



