var map = {
  "0":"单节点",
  "1":"主从",
  "2":"Galera Cluster"
}
var status = {
  "0":"运行中",
  "1":"失败",
  "2":"成功",
  "3":"暂停",
  "4":"清理",
  "5":"重试",
  "6":"清理完成"
}
export default {
  data() {
    return {
      total: 0,
      current_page: 1,
      current: 1,  //默认值为1
      mysqlList:[]
    }
  },
  filters: {
    toMysqlText:function (value) {
      return map[value] || ''
    },
    toStatusText:function (value) {
      return status[value] || ''
    }
  },
  mounted(){
    this.setCurrent(1);
  },
  props: {
    // total: {// 数据总条数
    //   type: Number,
    //   default: 10
    // },
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    // currentPage: {// 当前页码
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods:{
    // 分页
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      window.scrollTo(0,0);
      //总条数
      this.$http.get('/acmeCloud/deploy/mysql/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.total = response_info.data;

        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
      // 分页  部署列表
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      // console.log(data);
      this.$http.get('/acmeCloud/deploy/mysql/list?'+
        "current_page="+this.current_page+"&page_size="+this.display).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.mysqlList = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
          // console.log(this.mysqlList)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    }
  }
}



