import Vue from 'vue';
export default {
  data(){
    return{
      instance_list:[],
      top_avg_list:[],
      topcount_list:[],
      time_data:[],
      show_pagination:true,
      currentPage:1,
      zero:'00:00:01',
      echeat_info:'实例暂无数据'
    }
  },
  filters:{
    toValue:function (value) {
      var queryvalue=Math.ceil(value);
      return queryvalue;
    }
  },
  mounted(){
    this.get_count('toptimesum');
    // this.change_tab(1);
    // this.change_query_time(1);
    },
  methods:{
    fmtDate(timeStamp){
      var date = new Date();
      date.setTime(timeStamp * 1);
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? ('0' + m) : m;
      var d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      var h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      var minute = date.getMinutes();
      var second = date.getSeconds();
      minute = minute < 10 ? ('0' + minute) : minute;
      second = second < 10 ? ('0' + second) : second;
      return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
    },
    change_query_time(e){
      var that = this;
      this.$http.get('/acmeCloud/slowQuery/instance/echart?'+
        'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&displayTime='+e).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          if(response_info.data.length ==0){
          }else{
            this.echeat_info ='';
          var data_time = response_info.data;
          var dd = new Array();
          var time = new Array();
          for(var i = 0;i<data_time.length;i++){
            dd[i] = data_time[i].query_time;
          }
          for(var i = 0;i<data_time.length;i++){
            time[i] = this.fmtDate(data_time[i].time);
          }
          this.change_query_draw(dd,time);
          }
        } else {
        }
      });
    },
    get_day(day){
      var today = new Date();
      var h = today.getHours(); //获取当前小时数(0-23)
      var m = today.getMinutes(); //获取当前分钟数(0-59)
      var s = today.getSeconds(); //获取当前秒数(0-59)
      if(s<10){
       s = '0'+ s
      }
      var targetday_milliseconds = today.getTime() + 1000*60*60*24*day;
      today.setTime(targetday_milliseconds); //注意，这行是关键代码
      var tYear = today.getFullYear();
      var tMonth = today.getMonth();
      var tDate = today.getDate();
      tMonth =this. doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      return tYear+"-"+tMonth+"-"+tDate+' '+ h+':'+m+':'+s;
      },
    doHandleMonth(month){
      var m = month;
      if(month.toString().length == 1){
        m = "0" + month;
      }
      return m;
    },
    change_tab(e){
      var that = this;
      this.$http.get('/acmeCloud/slowQuery/instance/echart?'+
        'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&displayTime='+e).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          if(response_info.data.length ==0){
          }else{
            this.echeat_info ='';
            var data_time = response_info.data;
            var cc = new Array();
            var time = new Array();
            for(var i = 0;i<data_time.length;i++){
              cc[i] = data_time[i].query_time;
            }
            for(var i = 0;i<data_time.length;i++){
              time[i] = this.fmtDate(data_time[i].time);
            }
            this.data_count(cc,time);
          }
        } else {
        }
      });
    },
    getCountDays() {
      var curDate = new Date();
      var curMonth = curDate.getMonth();
      curDate.setMonth(curMonth + 1);
      curDate.setDate(0);
      return curDate.getDate();
},
  get_count(e){
      if(e=='toptimesum'){
        this.$http.get('/acmeCloud/slowQuery/instance/toptimesum?'+
          'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&displayNum='+20+
          '&startTime='+  this.get_day(-7) +' '+'&endTime='+this.get_day(0)).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.instance_list = response_info.data;

          } else {
          }
        });
      }
        if(e=='topcount'){
          this.$http.get('/acmeCloud/slowQuery/instance/'+e+'?'+
              'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&startTime='+ this.get_day(-7) +'&endTime='+this.get_day(0)+'&displayNum='+20).then(function (res) {
            var response_info = res.body;
            if (response_info.status==0) {
             this.topcount_list = response_info.data;
            } else {
            }
          });
        }else if(e=='topavgtime'){
          this.$http.get('/acmeCloud/slowQuery/instance/'+e+'?'+
            'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&startTime='+ this.get_day(-7) +'&endTime='+this.get_day(0) +'&displayNum='+20).then(function (res) {
            var response_info = res.body;
            if (response_info.status==0) {
              this.top_avg_list = response_info.data;
            } else {
            }
          });
        }
      },
    data_count(e,t){//左边的echeart
      var num_left = this.$echarts.init(document.getElementById('num_left'));
      num_left.setOption({
        tooltip : {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        title:{
          text:'实例详情',
          textStyle:{
            color:'#847B7B',
            fontSize:20
          },
          top:0,
          left:0,
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine:{
            show:false
          },
          axisTick:{
            show:true
          },
          axisLabel:{
            show:false
          },
          data: t
        },
        yAxis: {
          type: 'value',
          axisLine:{
            show:false
          },
          axisLabel:{
            show:false
          },
          splitLine:{
            lineStyle:{
              color:'#192E3E',
              width:2
            }
          },
          splitArea:{
            areaStyle:{
              color:['rgba(189, 195, 39,1)']
            }
          }
        },
        series: [
          {
            type:'line',
            smooth: true,
            data:e,
            itemStyle:{
              color:'#0F8FC1',
              borderColor:'#000',
              shadowColor: 'rgba(15, 143, 193, 1)',
              shadowBlur: 10,
            },
            areaStyle:{
              color:'rgba(15, 143, 193,0.5)'
            },
            symbolSize:8
          },

        ]
      });
    },
    change_query_draw(e,t){
      var num_right = this.$echarts.init(document.getElementById('num_right'));
      num_right.setOption({
        title:{
          text:'实例详情',
          textStyle:{
            color:'#847B7B',
            fontSize:20,
          },
          top:0,
          left:0,
        },
        tooltip : {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine:{
            show:false
          },
          axisTick:{
            show:true
          },
          axisLabel:{
            show:false
          },
          data: t
        },
        yAxis: {
          type: 'value',
          axisLine:{
            show:false
          },
          axisLabel:{
            show:false
          },
          splitLine:{
            lineStyle:{
              color:'#192E3E',
              width:2
            }
          },
          splitArea:{
            areaStyle:{
              color:['rgba(189, 195, 39,1)']
            }
          }
        },
        series: [
          {
            type:'line',
            smooth: true,
            data:e,
            itemStyle:{
              color:'#FFC30D',
              borderColor:'#000',
              shadowColor: 'rgba(255, 195, 13, 1)',
              shadowBlur: 10,
            },
            areaStyle:{
              color:'rgba(189, 195, 39,0.5)'
            },
            symbolSize:8
          },

        ]
      })
    },
    get_instance_detail(){
      this.$http.get('/acmeCloud/slowQuery/instance/toptimesum?'+
        'hostIp='+this.$route.query.ip+'&instancePort='+this.$route.query.port+'&displayNum='+20+
        '&startTime='+  this.get_day(-7) +' '+this.zero +'&endTime='+this.get_day(0)+' '+this.zero).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.instance_list = response_info.data;

          } else {
          }
        });
    },
  }
}



