var List = {
  "0":" 正常",
  "1":"不可访问",
  "2":"下线"
}
export default {
  data() {
    return {
      show: -1,
      offAndOn: 1,//1为开On  2为off
      total: 0,
      currentPage: 1,
      current: 1,
      hostList: [],
      deleteHostName:""
    }
  },
  filters: {
    toHostIps:function (value) {
      var ips =value.replace(/,/g,'\n')
      // console.log(value)
      // console.log(ips)
      return ips
    },
    toHostList:function (value) {
      return List[value] || ''
    }
  },
  mounted() {
    this.setCurrent(1);
  },
  props: {
    // total: {// 数据总条数
    //   type: Number,
    //   default: 10
    // },
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    // currentPage: {// 当前页码
    //   type: Number,
    //   default: 1
    // },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        }
        ;
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      ;
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods: {
    deleteHost:function (host_name) {
      let data = {
        "host_name": host_name,
      }
      this.$http.post('/acmeCloud/host/delete',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("删除成功!")
          this.setCurrent(1);
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          alert("删除失败，请咨询DBA或者管理员!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    deployAgent: function (hostName) {
      // console.log(hostName)
      let data = {
        "host_name": hostName
      }
      this.show = -1;
    },
    // 分页
    setCurrent: function (idx) {
      // console.log(idx)
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.currentPage = this.current;
        this.$emit('pagechange', this.current);
      }
      window.scrollTo(0,0);
      //总条数
      this.$http.get('/acmeCloud/host/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.total = response_info.data;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
      // 分页  主机列表
      let data = {
        "currentPage": this.currentPage,
        "pageSize": this.display
      }
      // console.log(data)
      this.$http.get('/acmeCloud/host/list?'+
        "currentPage="+this.currentPage+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.hostList = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      });
    }
  }
}




