export default {
  data() {
    return {
      hostName:"",
      hostInfoName:"",  //主机名
      kernel_release:"", //内核版本
      host_status:"",  //状态
      remark:"",      //备注
      os_type:"",
      cpu_model:"",
      cpu_core:"",
      disk_total:"",   //磁盘容量
      business_contact:"",  //业务联系人
      mem_total:"",
      mem_total_format:"",
      disk_total_format:"",
      diskList:[],     //磁盘信息集合
      instanceList:[],   //节点信息集合
      up_remark:"",
      updateRemark:-1,    //修改备注
      host_name:""
    }
  },
  mounted(){
    if (this.$route.query.hostName) {
      sessionStorage.setItem("hostName", this.$route.query.hostName);
      this.hostName = this.$route.query.hostName;
    }else{
      this.hostName = sessionStorage.getItem("hostName");
    }
    // console.log(this.hostName);
    this.getHostInfo(this.hostName);
    this.getDiskInfo(this.hostName);
    this.getInstanceInfo(this.hostName);
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods:{
    //基本信息
    getHostInfo:function (hostName) {
      var param="?hostName="+hostName;
      this.$http.get('/acmeCloud/host/baseinfo'+param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.hostInfoName=response_info.data.host_name;
          this.kernel_release=response_info.data.kernel_release;
          this.host_status=response_info.data.host_status;
          this.remark=response_info.data.remark;
          this.up_remark=response_info.data.remark;
          this.os_type=response_info.data.os_type;
          this.cpu_model=response_info.data.cpu_model;
          this.cpu_core=response_info.data.cpu_core;
          this.disk_total=response_info.data.disk_total;
          this.business_contact=response_info.data.business_contact;
          this.mem_total=response_info.data.mem_total;
          this.disk_total_format=response_info.data.disk_total_format;
          this.mem_total_format=response_info.data.mem_total_format;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //修改备注
    updateHostInfoRemark:function () {
      let data={
        host_name:this.hostName,
        remark:this.up_remark
      }
      // console.log(data)
      this.$http.post('/acmeCloud/host/remark/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          this.updateRemark=-1;
          this.getHostInfo(this.hostName);
          // alert("修改成功!")
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    // 磁盘信息
    getDiskInfo:function (hostName) {
      var param="?hostName="+hostName;
      this.$http.get('/acmeCloud/host/diskinfo'+param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.diskList = response_info.data
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //节点信息
    getInstanceInfo:function (hostName) {
      var param="?hostName="+hostName+"&Value=";
      this.$http.get('/acmeCloud/host/instanceinfo'+param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status==0) {
          // 成功
          this.instanceList = response_info.data
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      });
    }
  }
}



