export default {
  data(){
    return{
        displayNum:10,
      comment_list:[],
      none_data:[],
    }
  },
  mounted(){
    this.last_comment();
    this.slow_query_num();
    this.get_lastday_count();
    this.last_day();
  },
  methods:{
    last_comment(){
      this.$http.get('/acmeCloud/slowQuery/lastcomment?'+'displayNum='+this.displayNum
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.comment_list = response_info.data;
        } else {

        }
      });
    },
    last_day(){
      this.$http.get('/acmeCloud/slowQuery/echart/lastday'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info);
          if(response_info.data.length == 0){
            // console.log('没有数据');
            this.none_data = '暂无最近慢查询数据';
          }else{
            var time_array = new Array();
            var slow_num =[];
            var slow_time =[];
            for(var i =0;i<response_info.data.length;i++){
              time_array.push(response_info.data[i].time);
              slow_num.push(response_info.data[i].slow_query_num);
              slow_time.push(response_info.data[i].slow_query_time);
            }
            var wave = this.$echarts.init(document.getElementById('wave'));
            wave.setOption({
              tooltip:{
                show:true
              },
              title:{
                text:'最近一个月的慢查询次数',
                textStyle:{
                  color:'#fff'
                },
                top:10,
                left:10
              },
              xAxis: {
                show:true,
                axisLine:{
                  show:true
                },
                type: 'category',
                data: time_array,
                axisTick:{
                  show:true
                },
                splitLine:{
                  show:true,
                  lineStyle:{
                    color:'#fff',
                  }
                },
                axisLabel:{
                  show:false
                }
              },
              yAxis: {
                show:true,
                splitLine:{
                  show:true,
                  lineStyle:{
                    color:'#405867',
                  }
                },
                axisLabel:{
                  show:false
                }
              },
              series: [{
                label:{
                  show:true
                },
                data: slow_num,
                type: 'line',
                smooth: true,
                symbolSize:8,
                symbol:'circle',
                areaStyle:{
                  color:'rgba(25,53,68,0.5)'
                },
                itemStyle:{
                  color:'#00B7EE',
                  borderType:'solid',
                },
              }]
            })
          }

        } else {
          // console.log('没有数据');
          this.none_data = '暂无最近慢查询数据';
        }
      });
    },
    get_floor_num(){
      return  Math.floor(Math.random()*1000)
    },
    get_lastday_count(){
      this.$http.get('/acmeCloud/slowQuery/lastdaycount').then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
           // console.log(response_info.data);
         // console.log(this.get_floor_num());
          var c = response_info.data.total;
          var slowquery_num = this.$echarts.init(document.getElementById('slowquery_num'));
          slowquery_num.setOption({
            title:[{
              text:'慢查询数',
              textStyle:{
                color:'#fff'},
              bottom:100,
              right:10
            },{
              text:c,
              textStyle:{
                color:'#fff',
              fontSize:50},
              bottom:85,
              right:100
            }],
            series: [
              {
                name:'慢查询总数',
                type:'pie',
                radius: ['55%', '67%'],
                center:['25%','50%'],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: true,
                    textStyle: {
                      fontSize: '30',
                      fontWeight: 'bold'
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data:[
                  // {value:135, name:'慢查询总数'},
                  // {value:310, name:'慢查询'},
                  {value:this.get_floor_num(), name:'慢查询总数'},
                  {value:this.get_floor_num(), name:'慢查询'},
                ],
                color:['#00B7EE','#ADCBD4'],
              }
            ]
          });
        } else {
        }
      });
    },
    slow_query_num(){
      // var wave = this.$echarts.init(document.getElementById('wave'));
      // wave.setOption({
      //   tooltip:{
      //     show:true
      //   },
      //   xAxis: {
      //     type: 'category',
      //     data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
      //     axisTick:{
      //       show:false
      //     },
      //     splitLine:{
      //       show:true,
      //       lineStyle:{
      //         color:'#405867',
      //         type:'dotted'
      //       }
      //     },
      //     axisLabel:{
      //       show:false
      //     }
      //   },
      //   yAxis: {
      //     show:true,
      //     splitLine:{
      //       show:true,
      //       lineStyle:{
      //         color:'#405867',
      //       }
      //     },
      //     axisLabel:{
      //       show:false
      //     }
      //   },
      //   series: [{
      //     data: [820, 932, 901, 934, 1290, 1330, 1320],
      //     type: 'line',
      //     smooth: true,
      //     symbolSize:8,
      //     symbol:'circle',
      //     areaStyle:{
      //       color:'rgba(25,53,68,0.5)'
      //     },
      //     itemStyle:{
      //       color:'#00B7EE',
      //       borderType:'solid',
      //     },
      //   }]
      // })
      var monitor = this.$echarts.init(document.getElementById('monitor'));
      monitor.setOption({
        tooltip:{
          show:true
        },
        legend:{
          type:'plain',
          right:10,
          orient:'vertical',
          textStyle:{
            color:'#fff',
            lineHeight:30,
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine:{
            show:false
          },
          axisTick:{
            show:true
          },
          axisLabel:{
            color:'#fff'
          },
          data: ['6:00pm','6:00pm','6:00pm','6:00pm','6:00pm','6:00pm','6:00pm']
        },
        yAxis: {
          type: 'value',
          axisLine:{
            show:false
          },
          axisLabel:{
            color:'#fff'
          },
        },
        series: [
          {
            name:'MYSQL',
            type:'line',
            data:[230, 132, 101, 224, 90, 230, 210],
            itemStyle:{
              color:'#FFC30D',
              borderColor:'#FFC30D',
              shadowColor: 'rgba(255, 195, 13, 1)',
              shadowBlur: 10
            },
            symbolSize:12
          },
          {
            name:'主机',
            type:'line',
            data:[220, 182, 191, 204, 290, 130, 310],
            itemStyle:{
              color:'#0DF6FF',
              borderColor:'#0DF6FF',
              shadowColor: 'rgba(13, 246, 255, 1)',
              shadowBlur: 10
            },
            symbolSize:12
          },

        ]
      })
    }
  }
}



