export default {

  data(){
    return {
      userdetail:[],
      // base_active:'active',
      connect_active:'active',
      account_active:'',
      innodb_active:'',
      lock_active:'',
      tablist:[],
      show_alert:false,
      change_result:false,
      remark_change:true,
      change_remark_instance:'暂无备注',
      list_name:'base',
      configlist:[],
      connectlist:[],
      accontlist:[],
      innodblist:[],
      locklist:[],


    }
  },
  created(){
    this.get_status();
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        el.focus()
      }
    }
  },
  mounted(){
    this.list_url('baseinfo');
    // this.get_info();
   this.get_session();
   this.get_account();
  this.get_innoDB();
   this.dead_lock();
   this.mangement('session');
  },
  methods: {
    dead_lock(){
      this.$http.get('/acmeCloud/MySQLInstance/deadlock?'+
        "hostIp="+this.$route.query.ip+'&instancePort='+this.$route.query.port).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info);
          this.locklist = response_info.data;
        } else {
        }
      });
    },
    get_innoDB(){
      this.$http.get('/acmeCloud/MySQLInstance/innodbstatus?'+
        "hostIp="+this.$route.query.ip+'&instancePort='+this.$route.query.port).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info);
          this.innodblist = response_info.data;
        } else {
        }
      });
    },
    get_account(){
      this.$http.get('/acmeCloud/MySQLInstance/account?'+
        "hostIp="+this.$route.query.ip+'&instancePort='+this.$route.query.port).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.accontlist = response_info.data;
        } else {
        }
      });
    },
    get_status(){
      if(this.$route.query.connect){
        this.base_active = '';
          this.connect_active ='active';
      }
      if(this.$route.query.lock){
        this.base_active = '';
        this.lock_active ='active';
      }
      if(this.$route.query.innodb){
        this.base_active = '';
        this.innodb_active ='active';
      }
      if(this.$route.query.account){
        this.base_active = '';
        this.account_active ='active';
      }
    },
    get_session(){
      this.$http.get('/acmeCloud/MySQLInstance/session?'+
        "hostIp="+this.$route.query.ip+'&instancePort='+this.$route.query.port).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info);
          // this.tablist = response_info.data;
        } else {
        }
      });
    },
    change_remark(){
      this.remark_change = !this.remark_change;
      this.tablist.remark = null;
      this.change_remark_instance='';
    },
    remark_modify(e){
      var that = this;
      if(e==2){
       this.remark_change = true;
      }else{
        var n ={
          instance_name:that.$route.query.name,
          remark:$.trim(that.change_remark_instance),
        };
        that.$http.post('/acmeCloud/MySQLInstance/remark/modify?',
          JSON.stringify(n), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            emulateJSON: true
          }).then(function (res) {
          var response_info = res.body;
          that.tablist.remark = $.trim(that.change_remark_instance);
          that.remark_change = true;
          if (response_info.status == 0) {
            // console.log('修改成功');
            that.show_alert=true;
            setTimeout(function () {
              that.show_alert=false;
            },1000)
          } else {

          }
        });
      }
    },
    get_info(){
      this.$http.get('/acmeCloud/MySQLInstance/statistics?'+
        "instanceName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          var color_list = response_info.data;
          var color_cheart = this.$echarts.init(document.getElementById('color_cheart'));
          color_cheart.setOption({
            tooltip:{
              show:true
            },
            title:{
              text:'实例：'+this.$route.query.name,
              textStyle:{
                color:'#fff'
              },
              top:10,
              left:10,
            },
            xAxis:{
              type:'category',
              data:['TPS','QPS','LSN','扫描行','cpu','网络吞吐','复制延迟'],
              show:true,
              axisLine:{
                show:false
              },
              splitLine:{
                show:false
              },
              axisLabel:{
                show:true,
                textStyle:{
                  color:'#04C7D9'
                },
                padding:[15,10,15,10],
                borderRadius:55,
                backgroundColor:'#142B38'
              }
            },
            yAxis:{
              type:'value',
              show: true,
              splitLine: {
                show: true,
                lineStyle:{
                  color:'#193040',
                  width:2,
                }
              },
              axisLine: {
                show: false
              },
              axisLabel: {
                color: '#294556'
              },
              axisTick:{
              },
            },
            series:[].concat(
              {
                type:'pictorialBar',
                itemStyle: {
                  normal: {
                    color: function(params) {
                      var colorList = [
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#44A1BC' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#7B54DB' // 100% 处的颜色
                          }],
                          itemStyle:{
                            color:'#fff',
                            fontSize:22
                          },
                          globalCoord: false
                        },
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#76C266' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#D2A72E' // 100% 处的颜色
                          }],
                          globalCoord: false
                        },
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#A14BB0' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#9B8375' // 100% 处的颜色
                          }],
                          globalCoord: false
                        },
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#11D8AF' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#1FA2D6' // 100% 处的颜色
                          }],
                          globalCoord: false
                        },
                        '#D2A72E',
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#E05138' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#B335AE' // 100% 处的颜色
                          }],
                          globalCoord: false
                        },
                        {
                          type: 'linear',
                          x: 1,
                          y: 0,
                          x2: 0,
                          y2: 1,
                          colorStops: [{
                            offset: 0, color: '#F33A39' // 0% 处的颜色
                          }, {
                            offset: 1, color: '#EE832B' // 100% 处的颜色
                          }],
                          globalCoord: false
                        }
                      ];
                      return colorList[params.dataIndex]
                    },
                    label: {
                      show: false,
                    }
                  }
                },
                symbolMargin: '60%',
                data:[
                  { value:color_list.instance_tps,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                  {
                    value:color_list.instance_qps,
                    symbolRepeat: true,
                    symbolSize: [40, 20]

                  },
                  { value:color_list.instance_lsn,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                  { value:color_list.instance_rows,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                  { value:color_list.instance_cpu,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                  { value:color_list.instance_net_inout,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                  { value:color_list.instance_behind_master,
                    symbolRepeat: true,
                    symbolSize: [40, 20]
                  },
                ],
                symbol: 'Rect',
              })
          })

        } else {
        }
      });
    },
    list_url(e){
      this.$http.get('/acmeCloud/MySQLInstance/'+e+'?'+
        "instanceName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.tablist = response_info.data;
        } else {
        }
      });
    },
    mangement(e){
      this.$http.get('/acmeCloud/MySQLInstance/'+e+'?'+
        "hostIp="+this.$route.query.ip+'&instancePort='+this.$route.query.port).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.userdetail = response_info.data;
        } else {
        }
      });
    }
  }
}



