export default {
  data(){
    return{
      template_id:"",
      template_name:"",
      remark:"",
      operator:"",
      create_time:"",
      update_time:"",
      version:"",
      updateTemplateName:"",   //修改名称
      updateTemplateRemark:"",  //修改备注
      baseInfoList:"",//基本详情
      infoList:[],     //参数详情
      isUpName:-1,
      isUpRemark:-1,
      isUpParam:-1,   //是否编辑0.不编辑    1.编辑
      upParamName:"123",     //参数名
      upParamValue:"",
    }
  },
  mounted(){
    if (this.$route.query.template_id) {
      // sessionStorage.setItem("template_id", this.$route.query.template_id);
      this.template_id = this.$route.query.template_id;
    }
    // else{
    //   this.template_id = sessionStorage.getItem("template_id");
    // }
    this.baseInfo(this.template_id);      //参数详情
    this.templateInfo(this.template_id);  //页面下面的模板参数详情
  },
  methods:{
    //修改参数详细
    upParam:function(paramName,paramValue){
      this.isUpParam=paramName;
      this.upParamValue=paramValue;
    },
    //恢复默认值
    rvNameAndValue:function(paramValue){
      // console.log(paramValue)
      // this.upParamName=paramName;
      this.upParamValue=paramValue;
    },
    //批量修改
    modifyParam:function(){
      let data={
        "template_id":this.template_id,
        "parameter_template_list":this.infoList
      }
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/info/batch/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          this.templateInfo(this.template_id)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //保存参数昵称与值
    saveParamNameAndVl:function(paramName,parameter_value){
      this.isUpParam=0;
      let data={
        "template_id":this.template_id,
        "parameter_name":paramName,
        "parameter_value":parameter_value
      }
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/info/single/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          this.templateInfo(this.template_id);
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //修改模板昵称
    updateUserName:function(){
      let data={
        templateId:this.template_id,
        templateName:this.template_name
      }
      // console.log(data);
      this.$http.post('/acmeCloud/parameter/info/name/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          location.reload();
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //修改模板描述
    updateRemark:function () {
      let data={
        templateId:this.template_id,
        remark:this.remark
      }
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/info/remark/modify',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          location.reload();
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //基本信息
    baseInfo:function (template_id) {
      var pram = "?templateId="+template_id;
      this.$http.get('/acmeCloud/parameter/baseinfo'+pram).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.template_id=response_info.data.template_id;
          this.template_name=response_info.data.template_name;
          this.remark=response_info.data.remark;
          this.operator=response_info.data.operator;
          this.version=response_info.data.version;
          this.create_time=response_info.data.create_time;
          this.update_time=response_info.data.update_time;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      })
    },
    //参数模板详情
    templateInfo:function (template_id) {
      var pram = "?templateId="+template_id
      this.$http.get('/acmeCloud/parameter/info'+pram).then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.infoList=response_info.data;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      })
    },
    // 模板名称正则
    blurFn:function () {
      var ipt=document.getElementsByTagName("input");
      //模板名称
      var reg=/^[\u4e00-\u9fa5]{3,8}$/;//用户名为3-8个字符(只支持中文)
      if(ipt[0].value==""){
        ts0.innerHTML="内容不能为空";
        ts0.style.color="red";
      }
      else if(reg.test(ipt[0].value)){
        ts0.innerHTML="";
        ts0.style.color="green";

      }
      else{
        ts0.innerHTML="格式错误";
        ts0.style.color="red";
        ipt[0].value="";//输入错误时输入框就清空
      };
    }
  }
}




