export default {
  data(){
    return {
      confirm_backup:'',
      centerDialogVisible: false,//删除的弹窗
      total: 0,
      current_page: 1,
      current: 1,
      no_active:'',
      no_disabled:'disabled',
      change_result : '暂停成功',
      currentPage:1,
      pageSize:10,
      show_pagination:false,
      show_alert:false,
      hostlist: [],
    pxclist:[],
      singlelist:[]
    }
  }
  ,
  mounted(){
    // this.get_backup_list();
    this.setCurrent(1);
  },
  props: {
    display: {// 每页显示条数
      type: Number,
      default: 10
    },
    pagegroup: {// 分页条数
      type: Number,
      default: 5,
      coerce: function (v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function () { // 总页数
      return Math.ceil(this.total / this.display);
    },
    grouplist: function () { // 获取分页页码
      var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({text: this.page - len, val: this.page - len});
        };
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      };
      var idx = temp.indexOf(center);
      (idx < count) && ( center = center + count - idx);
      (this.current > this.page - count) && ( center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
        (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
      }
      return list;
    }
  },
  methods: {
    // 分页
    setCurrent: function (idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.current_page = this.current;
        this.$emit('pagechange', this.current);
      }
      //总条数
      this.$http.get('/acmeCloud/MySQLCluster/total').then(function (res) {
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.total = response_info.data;

        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
      });
      // 分页  部署列表
      let data = {
        "current_page": this.current_page,
        "page_size": this.display
      }
      this.$http.get('/acmeCloud/backup/list?'+
        "currentPage="+this.current_page+"&pageSize="+this.display).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.hostlist = response_info.data;
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
        }
      });
    },
   get_backup_list(){
     this.$http.get('/acmeCloud/backup/list?'+
       "currentPage="+this.currentPage+"&pageSize="+this.pageSize).then(function (res) {
       var response_info = res.body;
       if (response_info.status==0) {
         this.hostlist = response_info.data;
         if(response_info.data.length/10>=1){
           this.show_pagination = true;
           this.cluster_page_num = Math.ceil(response_info.data.length/10/10);
         }
       } else {
         // console.log(response_info);
       }
     });
   },
    go(e){//翻页
      var that = this;
      var num = e;//页数
      this.currentPage = e;
      this.$http.get('/acmeCloud/backup/list?'+
        "currentPage="+e +"&pageSize="+this.pageSize).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          if(response_info.data.length < 10){
           this.no_active = true;
            this.hostlist = response_info.data;
          }else{
            this.hostlist = response_info.data;
            this.no_active = false;
          }
        } else {
          this.no_active = true;
        }
      });
    },
    state_change(e,n){
      var that = this;
      var data={
        clusterName:n
      };
      this.$http.post('/acmeCloud/backup/'+e+'?clusterName='+n,
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          if(e=='switch'){
            that.change_result='暂停成功';
            that.show_alert=true;
            setTimeout(function () {
              that.show_alert=false;
            },1000)
          }
          if(e=='delete'){
            that.change_result='删除成功';
            that.show_alert=true;
            setTimeout(function () {
              that.show_alert=false;
            },1000)
          }
          if(e=='deploy'){
            that.change_result='部署成功';
            that.show_alert=true;
            setTimeout(function () {
              that.show_alert=false;
            },1000)
          }
        }
      });
    },
    delete_backup(e,f){
      this.confirm_backup =e;
      this.confirm_backup_index = f;
      // this.centerDialogVisible = true;
    },
    sure_delete(){
      var that = this;
      // that.centerDialogVisible = false;
      var data={
        clusterName:that.confirm_backup
      };
      // console.log(data);
      that.$http.post('/acmeCloud/backup/delete'+'?clusterName=',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          that.setCurrent(1);
        }
      });
    },
  }

}



