var map = {
  "0":"内存使用率",
  "1":"swap使用率",
  "2":"磁盘使用率",
  "3":"inode使用率",
  "4":"实例数据大小",
  "5":"binlog大小",
  "6":"自增键使用率检查",
  "7":"myisam表",
  "8":"大表检查",
  "9":"表碎片",
  "10":"索引个数",
  "11":"冗余索引",
  "12":"从库可写",
  "13":"临时表检查",
  "14":"外键检查",
  "15":"没有主键"
}
var mapCom = {
  "0":"使用百分比",
  "1":"使用百分比",
  "2":"使用百分比",
  "3":"使用百分比",
  "4":"单位：兆(M)",
  "5":"单位：兆(M)",
  "6":"使用百分比",
  "7":"1：显示包含myisam表 0：不显示",
  "8":"单位：兆(M)",
  "9":"碎片率百分比",
  "10":"单位：个",
  "11":"1：显示冗余索引 0：不显示冗余索引",
  "12":"1：显示从库可写 0：不显示从库可写",
  "13":"1：显示临时表 0：不显示",
  "14":"1：显示外键的表 0：不显示",
  "15":"1：显示无主键的表  0：不显示"
}
export default {
  data() {
    return {
      hostConList: [],
      sqlConList: [],
      isUpdate: -1,
      isUpdateMysql:-1
    }
  },
  filters: {
    toHostText:function (value) {
      return map[value] || ''
    },
    toHostCom:function (value) {
      return mapCom[value] || ''
    },
  },
  mounted() {
    this.getConfigList()
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    selTxt:function(index){
      document.getElementById('#'+index)
    },
    //修改运算符及阈值
    upModify:function(host){
      this.isUpdate=-1;
      this.isUpdateMysql=-1;
      // console.log(host)
      host.threshold = parseInt(host.threshold)
      // console.log(JSON.stringify(host))
      this.$http.post('/acmeCloud/inspection/config/modify',
        JSON.stringify(host), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status==0) {
          alert("修改成功!")
          this.getConfigList()
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("加入失败...")
          alert("修改失败!")
        }
      }, function (res) {
        // console.log(res)
      });
    },
    //获取列表
    getConfigList: function () {
      this.$http.get('/acmeCloud/inspection/config/list').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          // 成功
          this.hostConList = response_info.data.host_inspection_conf;
          this.sqlConList = response_info.data.mysql_inspection_conf
          // console.log(this.hostConList);
          // console.log(this.sqlConList)
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      }, function (res) {
        // console.log(res)
      })
    }
  }
}



