import Vue from 'vue';
import Router from 'vue-router';
import VueResource from 'vue-resource';
import VueCookies from 'vue-cookies';
import Vuex from 'vuex';
import footer from '../components/footer.vue';
import content from '../components/content.vue';
import system from '../children/template/survey/system.vue';
import agentconfigure from '../children/template/entrycenter/agentconfigure.vue';
import intoLog from '../children/template/entrycenter/intoLog.vue';
import installConfiguration from '../children/template/deploycenter/installConfiguration.vue';
