<template>
  <div id="app" style="background: #232f3f;height: 100%">
    <v-header></v-header>
    <v-content></v-content>
    <v-rightPage></v-rightPage>
    <router-view></router-view>
  </div>
</template>

<script>
  import header from '@/components/header.vue';
  import footer from '@/components/footer.vue';
  import rightPage from '@/components/rightPage.vue';
  import content from '@/components/content.vue';

  export default {
    data() {
      return {
        is_login: this.$cookies.get("is_login")
      }
    },
    name: 'App',
    components: {
      'v-header': header,
      'v-content': content,
      'v-rightPage':rightPage
//      'v-footer': footer
    },
    mounted() {
     this.judge();
      //判断登录
//      console.log(this.$cookies.get("is_login"));
//      this.download_show();
    },
    methods: {
      judge:function () {
        var url=location.href;
        var is_register=url.indexOf('/register');
        if((this.is_login==0||this.is_login==null)&&is_register==-1)  {
          this.$router.push({path: '/login'});
        };
      },
      download_show:function () {
        this.$router.replace({path: '/download'});
      }
    }
  }

</script>

<style>
  @import "assets/css/common.css";
</style>



