    getOptionName: function () {
      this.$http.get('/arkgate/instancelist').then(function (res) {
        var response_info = res.body;
        if (response_info.status == 0) {
          this.srcName = response_info.data
          this.dstName = response_info.data
          if(this.srcName&&this.srcName.length>0){
            this.srcInstance=this.srcName[0].instance_name
          }
          if(this.dstName&&this.dstName.length>0){
            this.dstInstance=this.dstName[0].instance_name
          }
          // console.log(this.srcName)
          // console.log(this.dstName)
          // console.log("获取实例名称列表")
        }else {}
      })
    },
