    //预检查
    checkbeforecommit: function () {
      if(this.renameList.length>0){
        for (var i=0;i<this.renameList.length;i++){
          this.renameList[i].task_name=this.tunnelName
        }
      }
      if(this.columnList.length>0){
        for (var i=0;i<this.columnList.length;i++){
          this.columnList[i].task_name=this.tunnelName
        }
      }
      if(this.ddlList.length>0){
        for (var i=0;i<this.ddlList.length;i++){
          this.ddlList[i].task_name=this.tunnelName
        }
      }
      if(this.tunnelList.length>0){
        for (var i=0;i<this.tunnelList.length;i++){
          if(this.tunnelList[i].port){
            this.tunnelList[i].port=parseInt(this.tunnelList[i].port)
          }else{
            this.tunnelList[i].port=null
          }
        }
      }
      let data = {
        src_dst_info: {
          "task_name":this.taskName,
          "src_type":parseInt(this.sourceType),
          "src_instance": this.srcInstance,
          "src_ip":this.srcIp,
          "src_port":parseInt(this.srcPort),
          "src_user": this.srcUser,
          "src_pass": this.srcPass,
          "binlog_file": this.binlogFile,
          "binlog_pos": parseInt(this.binlogPos),
          "dst_type": parseInt(this.dst_type),
          "db_type":parseInt(this.db_type),
          "dst_instance": this.dstInstance,
          "dst_user": this.dstUser,
          "dst_pass": this.dstPass,
          "dst_ip": this.dstIP,
          "dst_port": parseInt(this.dstPort),
        },
        "gate_info": {
          "tunnel": this.tunnelName,
          "gate_cluster": this.gateCluster,
          "gatedb_user": this.gatedbUser,
          "gatedb_pass": this.gatedbPass,
          "gate_parameter": {
            "arkgate_parallel_workers": parseInt(this.arkgate_parallel_workers),
            "arkgate_worker_queue_length": parseInt(this.arkgate_worker_queue_length),
            "arkgate_replication_source": parseInt(this.arkgate_replication_source),
            "arkgate_json_full_image": parseInt(this.arkgate_json_full_image),
            "arkgate_binlog_expire_hours": parseInt(this.arkgate_binlog_expire_hours),
            "arkgate_delete_chunk_size": parseInt(this.arkgate_delete_chunk_size),
            "arkgate_delete_workers": parseInt(this.arkgate_delete_workers)
          },
          "filter": this.lists,
          "src_slave":this.tunnelList,
        },
        "etl_info": {
          "ip":this.ip,
          "path":this.path,
          "task_name":this.tunnelName,
          "etl_parameter": {
            "task_name":this.tunnelName,
            "thread_num": parseInt(this.threadNum),
            "start_id": parseInt(this.startId),
            "limit_query_rows": parseInt(this.limitQueryRows),
            "buffer_size":parseInt(this.bufferSize),
            "double_active": this.doubleActive,
            "exec_mode": this.execMode,
            "biz_conflict_api":this.biz_conflict_api
          },
          "rename": this.renameList,
          "filter_col": this.columnList,
          "filter_opt": this.ddlList
        }
      }

      // console.log(data)
      // console.log(111)
      //   console.log(this.threadNum)
      // console.log(this.limitQueryRows)
      // console.log(this.execMode)
      this.$http.post('/arkgate/checkbeforecommit',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          this.resultLists=response_info.data
          if(this.resultLists.length>0){
            for (var i=0;i<this.resultLists.length;i++){
              if(this.resultLists[i].check_status===1){
                this.isSubmit=false
                break
              }else{
                this.isSubmit=true
              }
            }
          }
          // console.log(this.resultLists)
        } else {

        }
      }, function (res) {
        // console.log(res)
      });
    },
    commitTask:function () {
      this.isSubmit=false
      // console.log("点了提交")
      if(this.renameList.length>0){
        for (var i=0;i<this.renameList.length;i++){
          this.renameList[i].task_name=this.tunnelName
        }
      }
      if(this.columnList.length>0){
        for (var i=0;i<this.columnList.length;i++){
          this.columnList[i].task_name=this.tunnelName
        }
      }
      if(this.ddlList.length>0){
        for (var i=0;i<this.ddlList.length;i++){
          this.ddlList[i].task_name=this.tunnelName
        }
      }
      if(this.tunnelList.length>0){
        for (var i=0;i<this.tunnelList.length;i++){
          if(this.tunnelList[i].port){
            this.tunnelList[i].port=parseInt(this.tunnelList[i].port)
          }else{
            this.tunnelList[i].port=null
          }
        }
      }
      let data = {
        src_dst_info: {
          "task_name":this.taskName,
          "src_type":parseInt(this.sourceType),
          "src_instance": this.srcInstance,
          "src_ip":this.srcIp,
          "src_port":parseInt(this.srcPort),
          "src_user": this.srcUser,
          "src_pass": this.srcPass,
          "binlog_file": this.binlogFile,
          "binlog_pos": parseInt(this.binlogPos),
          "dst_type": parseInt(this.dst_type),
          "db_type":parseInt(this.db_type),
          "dst_instance": this.dstInstance,
          "dst_user": this.dstUser,
          "dst_pass": this.dstPass,
          "dst_ip": this.dstIP,
          "dst_port": parseInt(this.dstPort),
        },
        "gate_info": {
          "tunnel": this.tunnelName,
          "gate_cluster": this.gateCluster,
          "gatedb_user": this.gatedbUser,
          "gatedb_pass": this.gatedbPass,
          "gate_parameter": {
            "arkgate_parallel_workers": parseInt(this.arkgate_parallel_workers),
            "arkgate_worker_queue_length": parseInt(this.arkgate_worker_queue_length),
            "arkgate_replication_source": parseInt(this.arkgate_replication_source),
            "arkgate_json_full_image": parseInt(this.arkgate_json_full_image),
            "arkgate_binlog_expire_hours": parseInt(this.arkgate_binlog_expire_hours),
            "arkgate_delete_chunk_size": parseInt(this.arkgate_delete_chunk_size),
            "arkgate_delete_workers": parseInt(this.arkgate_delete_workers)
          },
          "filter": this.lists,
          "src_slave":this.tunnelList,
        },
        "etl_info": {
          "ip":this.ip,
          "path":this.path,
          "task_name":this.tunnelName,
          "etl_parameter": {
            "task_name":this.tunnelName,
            "thread_num": parseInt(this.threadNum),
            "start_id": parseInt(this.startId),
            "limit_query_rows": parseInt(this.limitQueryRows),
            "buffer_size":parseInt(this.bufferSize),
            "double_active": this.doubleActive,
            "exec_mode": this.execMode,
            "biz_conflict_api":this.biz_conflict_api
          },
          "rename": this.renameList,
          "filter_col": this.columnList,
          "filter_opt": this.ddlList
        }
      }
      // console.log(data)
      this.$http.post('/arkgate/commit',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          this.$router.replace({path: 'synchronDetails?' + 'task_id=' + response_info.data});
          // console.log("提交成功")
        } else {
          this.$notify.error({
            title:'提交失败',
            message: '请联系管理员或DBA'
          });
        }
      }, function (res) {
        // console.log(res)
      });
    },
