export default {
  data(){
    return{
      message_tips:'',
      common_tips:'',
      common_status_list:[
        {
          value: 1,
          label: '待分析'
        }, {
          value: 2,
          label: '分析中'
        },
        {
          value: 3,
          label: '待上线'
        }, {
          value: 4,
          label: '已上线'
        },
        {
          value: 5,
          label: '重分析'
        },{
          value: 6,
          label: '已解决'
        }
      ],
      common_status:'待分析',
      info_list:[],
      history_list:[],
      comments_list:[],
      data_ins:{
        checksum_info:{
          db:[],
          examin_sum:[],
          examin_max:[],
          examin_min:[]
        },
        instance_info:{
          host_name:'',
          cluster_name:{},
          instance_role:''
        },
        query_review_info:{
          check_sum:[],
          first_seen:[],
          last_seen:[]
        }
      },
      explain_list:[],
      message:'',
      host_name:[],
      remnant:150,
    }
  },
  filters:{
    toValue:function (value) {
      var queryvalue=Math.ceil(value);
      return queryvalue;
    }
  },
  mounted(){
    this.get_instance_info();
   // this.get_cluste_list();
    this.get_comments_list();
    this.get_explain();
    this.get_history();
  },
  methods:{
    descInput(){
      var txtVal = this.message.length;
      this.remnant = 150 - txtVal;
    },
    post_comments(){//添加评论
      var that = this;
      if(that.common_status ==''){
        that.common_tips = '评论状态不能为空'
      }
      if(that.common_status=='待分析'){
        that.common_status =1
      }
      if(!that.message){
        that.message_tips = '评论不能为空';
      }
      if(that.message&&that.common_status){
        var n = new Object();
        n.cluster_name=this.data_ins.instance_info.cluster_name;
        n.instance_name = this.$route.query.instanceName;
        n.checksum =  this.$route.query.checkSum;
        n.sample =this.$route.query.sql;
        n.status = this.common_status;
        n.comments = this.message;
        this.$http.post('/acmeCloud/slowQuery/checksum/commentadd?',
          JSON.stringify(n), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            emulateJSON: true
          }).then(function (res) {
          var response_info = res.body;
          if (response_info.status == 0) {
            that.get_comments_list();
            that.message='';
          } else {
            alert('评论失败 请稍后重试')
          }
        });
      }
    },

    get_explain(){
      if(this.$route.query.dbName){
        this.$http.get('/acmeCloud/slowQuery/checksum/explain?'+
          'instancePort='+this.$route.query.port+'&sqlSample='+this.$route.query.sql+'&dbName='+this.$route.query.dbName+'&hostIp='+this.$route.query.hostip).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.explain_list = response_info.data;
          } else {
          }
        });
      }else{
        this.$http.get('/acmeCloud/slowQuery/checksum/explain?'+
          'instancePort='+this.$route.query.port+'&sqlSample='+this.$route.query.sql+'&dbName='+''
          +'&hostIp='+this.$route.query.hostip).then(function (res) {
          var response_info = res.body;
          if (response_info.status==0) {
            this.explain_list = response_info.data;
          } else {
          }
        });
      }
    },
    get_comments_list(){
      var that = this;
      that.$http.get('/acmeCloud/slowQuery/checksum/comments?'+
       'checkSum='+this.$route.query.checkSum).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          that.comments_list = response_info.data;
          // console.log( response_info);
          // console.log( this.comments_list );
        } else {
        }
      });
    },
    get_instance_info(){
      //console.log('get_instance_info');
      this.$http.get('/acmeCloud/slowQuery/checksum/instance?'+
        'instanceName='+this.$route.query.instanceName+'&checkSum='+this.$route.query.checkSum).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.data_ins = response_info.data;
        } else {
        }
      });
    },
    get_history(){
      this.$http.get('/acmeCloud/slowQuery/checksum/history?'+
        'hostIp='+this.$route.query.hostip+'&instancePort='+this.$route.query.port+'&checkSum='+this.$route.query.checkSum).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.history_list =  response_info.data.checksum_history_list;

        } else {
          this.history_list.length ==0;
        }
      });
    }
  }
}



