export default {
  data() {
    return {
      template_type:"",
      version:"MySQL5.7",
      MysqlList:[]      //参数列表集合
    }
  },
  mounted() {
    this.getMysqlList("1","MySQL5.7");
  },
  methods: {
    // 参数列表
    getMysqlList: function (template_type,version) {
      var param="?template_type="+template_type+"&version="+version;
      this.$http.get('/acmeCloud/parameter/list' + param).then(function (res) {
        // console.log(res);
        var response_info = res.body;
        // console.log(response_info);
        if (response_info.status == 0) {
          // 成功
          this.MysqlList=response_info.data;
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          // console.log("查询失败...")
        }
      })
    },
    //删除模板
    deleteTemplate:function (templateId) {
      let data = {
        "template_id": templateId,
      }
      // console.log(this.template_id);
      // console.log(data)
      this.$http.post('/acmeCloud/parameter/delete',
        JSON.stringify(data), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          emulateJSON: true
        }).then(function (res) {
        var response_info = res.body;
        // console.log(res);
        if (response_info.status == 0) {
          alert("删除成功!")
          this.getMysqlList("1");
        } else {
          // 说明调用接口失败，可能由于路径，网络问题
          alert("删除失败，请稍后再试!")
        }
      }, function (res) {
        // console.log(res)
      });
    }
  }
}



