      arkdb_deploy_tips: '',
      active: 0,
      architecture_type: 0,
      task_name: "", //任务名称
      deploy_port: "", //端口
      // init_user: "", //复制用户
      // init_password: "", //复制密码
      replication_user: "", //复制用户
      replication_password: "",//复制密码
      init_grant: "", //初始化授权
      namespace: "", //namespace名称
      version: "5.7", //版本
      base_dir: "",
      ipAddress: '',
      ipMaster: "",
      optionIps: [],
      slaves: [],
      pxc: [],
      template: "",
      MysqlList: [],
      // innodb_buffer_pool_size:"",
      // sync_binlog:"",
