export default {
  data(){
    return{
      machine:'',
    }
  },
  mounted(){
    this.get_cluster_count();
    // this.get_success_rate();
  },
  methods:{
    get_chart(){

      var datas =[96.3,96.4,97.5,95.6,98.1,94.8,89.6,94.1,80.1,52.4,75.8,94.7];
      option = {
        title: {
          text: '',
          textStyle: {
            fontWeight: 'normal',
            fontSize: 12,
            color: '#F1F1F3'
          },
          left: '6%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            lineStyle: {
              color: '#57617B'
            }
          }
        },
        legend: {
          icon: 'rect',
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          data: ['党费'],
          right: '4%',
          textStyle: {
            fontSize: 12,
            color: '#F1F1F3'
          }
        },
        grid: {
          left: '1%',
          right: '2%',
          bottom: '8%',
          top:'14%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#57617B'
            }
          },
          data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
          axisLabel:{
            interval:0,
            rotate:50,
            textStyle:{
              fontSize:12
            }
          },
        }],
        yAxis: [{
          type: 'value',
          interval: 40,
          max:120,
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#57617B'
            }
          },
          axisLabel: {
            textStyle: {
              fontSize: 12
            }
          },
          splitLine: {
            lineStyle: {
              color: '#57617B'
            }
          }
        }],
        series: [
          {
            name: '党费',
            type: 'line',
            smooth: true,
            lineStyle: {
              normal: {
                width: 2
              }
            },
            symbolSize:4,
            itemStyle: {
              normal: {
                color: 'rgb(242, 155, 118)',
                borderColor:'#e48b4c'
              },
            },
            data: datas,
          },{
            name: '党费2',
            type: 'line',
            smooth: true,
            lineStyle: {
              normal: {
                width: 2
              }
            },
            symbolSize:4,
            itemStyle: {
              normal: {
                color: 'rgb(27, 241, 172)',
                borderColor:'#e48b4c'
              },
            },
            data: [1,2,3,44,55,88],
          },

        ]
      };
    },
    get_success_rate(){
      this.$http.get('/acmeCloud/backup/successrate?'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info);
        } else {

        }
      });
    },
    get_cluster_count(){
      this.$http.get('/acmeCloud/backup/clustercount?'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
            var d = response_info.data;
            // console.log(response_info.data);
            this.draw(d);
        } else {

        }
      });
      this.$http.get('/acmeCloud/backup/datatotal?'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          var data_total = response_info.data;
          this.draw_data_total(data_total);
        } else {

        }
      });
      this.$http.get('/acmeCloud/backup/screen/machinemfs/usage?'
      ).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          this.machine = response_info.data;
        } else {

        }
      });
    },
    draw_data_total(data_t){
      var backup_cluster_echart = this.$echarts.init(document.getElementById('backup_cluster'));
      backup_cluster_echart.setOption({
        title:[{
          text:'备份数据总量',
          textStyle:{
            color:'#fff'},
            top:0,
          left:0
        },{
          text:'(单位G)',
          textStyle:{
            color:'red'},
          top:0,
          left:130
        }],
        color:['#0049B0','#DE5347'],
        series:[
          {
            type:'pie',
            radius:['80%','60%'],
            data:[
              {
                value:data_t,name:'数量',
                label: {
                  normal: {
                    position: 'center',
                    formatter: '{c|{c}} {text|}',
                    color: '#fff',
                    rich: {
                      c: {
                        fontWeight: 'bold',
                        color: '#B3BAC5',
                        fontSize: 40
                      },
                      text: {
                        color: '#fff'
                      }
                    }
                  }
                }
              },
              {
                value:200,

              },
            ]
          },
        ]
      })
    },
    draw(t){
      // console.log('draw');
      var copy = this.$echarts.init(document.getElementById('copy'));
      copy.setOption({
        title:{
          text:'备份集群数',
          textStyle:{
            color:'#fff',}
            },
        color:['#FFA25C','#148BBE'],
        series:[
          {
            type:'pie',
            radius:['80%','60%'],
            data:[
              {
                value:t,name:'数量',
                label: {
                  normal: {
                    position: 'center',
                    formatter: '{c|{c}} {text|}',
                    color: '#fff',
                    rich: {
                      c: {
                        fontWeight: 'bold',
                        color: '#FFA25C',
                        fontSize: 40
                      },
                      text: {
                        color: '#fff'
                      }
                    }
                  }
                }
              },
              {
                value:200,

              },
            ]
          },
        ]
      })
      // var base_num = this.$echarts.init(document.getElementById('base_num'));
      // base_num.setOption(
      //   {
      //     xAxis: {
      //       type: 'category',
      //       show:false,
      //       data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'],
      //     },
      //     yAxis: {
      //       type: 'value',
      //       show:true,
      //       axisLine:false,
      //       axisLabel:false,
      //       axisTick:false,
      //       splitLine:{
      //         show:true
      //       },
      //       axisLine:{
      //         lineStyle:{color:'#fff'}
      //       },
      //     },
      //     series: [{
      //       data: [820,550, 901, 640, 1290,555],
      //       type: 'line',
      //      areaStyle: {}
      //     }]
      //   }
      // )
      // var binlog = this.$echarts.init(document.getElementById('binlog'));
      // binlog.setOption(
      //   {
      //     xAxis: {
      //       type: 'category',
      //       show:false,
      //       data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'],
      //     },
      //     yAxis: {
      //       type: 'value',
      //       show:true,
      //       axisLine:false,
      //       axisLabel:false,
      //       axisTick:false,
      //       splitLine:{
      //         show:true
      //       },
      //       axisLine:{
      //         lineStyle:{color:'#fff'}
      //       },
      //     },
      //     series: [{
      //       data: [820,550, 901, 640, 1290,555],
      //       type: 'line',
      //       areaStyle: {}
      //     }]
      //   }
      // )
      var shade = this.$echarts.init(document.getElementById('shade'));
      shade.setOption( {
        legend: {
          icon: 'rect',
          top:'2%',
          right:'2%',
          data: ['备份成功率', '还原成功率'],
          textStyle: {
            fontSize: 12,
            color: '#F1F1F3'
          },
          orient:'vertical'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#57617B'
            }
          },
          data: ['1月', '2月', '3月', '4月', '5月'],
          z:[5,1]
        }],
        yAxis: [{
          type: 'value',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#57617B'
            }
          },
          axisLabel: {
            margin: 10,
            textStyle: {
              fontSize: 14
            }
          },
          splitLine: {
            lineStyle: {
              color: '#57617B'
            }
          }
        }],
        series: [{
          name: '备份成功率',
          type: 'line',
          lineStyle: {
            normal: {
              width: 1
            }
          },
          emphasis:{
            itemStyle:{
              color:'#fff',
              borderColor:'#fff',
              borderWidth:2,
              borderType:'solid',
            },
          },
          areaStyle: {
            normal: {
              shadowColor: 'rgba(0, 0, 0, 0.1)',
              shadowBlur: 10
            }
          },
          itemStyle: {
            normal: {
              color: 'rgb(242, 155, 118)'
            }
          },
          data: [123,145,562,222,444]
        }, {
          name: '还原成功率',
          type: 'line',
          lineStyle: {
            normal: {
              width: 1
            }
          },
          areaStyle: {
            normal: {
              shadowColor: 'rgba(0, 0, 0, 1)',
              shadowBlur: 10
            }
          },
          itemStyle: {
            normal: {
              color: 'rgb(27, 241, 172)'
            }
          },
          data: [197.3,299.2,399.3,100.0,599.6]
        }, ]
      })
    }
  }
}



