import Vue from 'vue';
export  default {
  data(){
    return {
      log_info:'',
      currentPage_set:1,
      pageSize_set:10,
      backup_info:[],
      currentPage:1,
      pageSize:10,
      backup_log:[],
      last:0,
      get_backup_colony_list :[
        {set_dir:'',
        show:''}
      ]
    }
  },
  mounted(){
    this.get_info();
    this.get_backupset();
    this.get_backup_colony();
    this.get_log();
  },
  methods:{
    get_log(){
      this.$http.get('/acmeCloud/backup/loginfo?'+
        "cluster_name="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info.data);
          if(response_info.data!=''){
            this.log_info = response_info.data;
          }
        } else {
        }
      });
    },
    get_info(){
      this.$http.get('/acmeCloud/backup/info?'+
        "clusterName="+this.$route.query.name).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
          // console.log(response_info.data);
          this.backup_info = response_info.data;
        } else {
        }
      });
    },
    check_log(e){
      var b = this.backup_log[this.last];//上一个index
      b.show=false;
      var a = this.backup_log[e];//点击显示的index
      this.last = e;
      a.show=true;
      Vue.set(this.backup_log,0, b);//隐藏上一个index
      Vue.set(this.backup_log,e, a);//显示index
    },
    get_backupset(){
      // this.$http.get('/acmeCloud/backup/backupset?'+
      //   "clusterName="+this.$route.query.name+'&currentPage='+this.currentPage+'&pageSize='+this.pageSize).then(function (res) {
      //   var response_info = res.body;
      //   if (response_info.status==0) {
      //     this.backup_log =response_info.data;
      //     this.backup_log.map(function(item){
      //       item.show = false;
      //     });
      //     this.backup_log[0].show = true;
      //   } else {
      //   }
      // });
    },
    get_backup_colony(){
      this.$http.get('/acmeCloud/backup/backupset?'+
        "clusterName="+this.$route.query.name+'&currentPage='+this.currentPage_set+'&pageSize='+this.pageSize_set).then(function (res) {
        var response_info = res.body;
        if (response_info.status==0) {
       // console.log(response_info);
       if(response_info.data){
         this.get_backup_colony_list = response_info.data;
       }
       // else{//没有集群列表
       // }
        } else {
        }
      });
    }
  }
}



